import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { BookingContext } from '../../contexts/BookingContext';
import M from "materialize-css";
import { CountryOptions } from '../Helper';
import Select from 'react-select';

const GuestInfo = ({post}) => {

    let classNames = require('classnames');

    const customStyles = {
        control: base => ({
            ...base,
            minHeight: 30,
            height: 45,
            border: 'none',
            borderBottom: '1px solid #9e9e9e'
        }),
        dropdownIndicator: base => ({
            ...base,
            padding: 4
        }),
        clearIndicator: base => ({
            ...base,
            padding: 4
        }),
        valueContainer: base => ({
            ...base,
            padding: '0px 16px',
            height: 30
        }),
        input: base => ({
            ...base,
            margin: 0,
            padding: 0
        }),
        // menu: base => ({
        //     ...base,
        //     maxHeight: '120px'
        // }),
        // menuList: base => ({
        //     ...base,
        //     maxHeight: '120px',
        // }),
    };

    const [ country ] = useState(CountryOptions);

    const { booking, setBooking, searchUserEmail } = useContext(BookingContext);


    const handleSubmit = (e) => {
        e.preventDefault();
        searchUserEmail();
    }

    useEffect(() => {
        var elems = document.querySelectorAll('select');
        M.FormSelect.init(elems);
    }, [])

    return ( 
        <div className={classNames({'row white z-depth-2 mobile': true})}>
            <div className="col s12">
                <div className="header-box">
                    Let us know who you are
                </div>
                <div className="row chr_mt10" style={{marginBottom: '15px'}}>
                    <div className="col s12">
                        <div className="chr_plf10">
                        <form onSubmit={handleSubmit}>

                            <div className="row">
                                <div className="col l8 s12">
                                    <label htmlFor="email">Email (*Confirmation email sent to this address)</label>
                                    <input type="email" name="email" placeholder="" value={booking.email} onChange={(e) => setBooking({...booking, email: e.target.value })} required className="validate"/>
                                    <span className="helper-text" data-error="Please check your email address" data-success="*Confirmation email sent to this address"></span>
                                </div>
                                <div className="input-field col l4 s12">
                                    <span onClick={handleSubmit} className={classNames({'waves-effect waves-light btn animated tada blue darken-3 valign-wrapper center-align chr_btn-active': true })} style={{marginTop: '6px'}}> 
                                        <i className={ classNames({ 'white-text material-icons':true }) }>
                                            search
                                        </i>
                                    </span>
                                    
                                    <span className="helper-text" data-error="wrong" data-success="right">* Search to Check whether you are our returning guest</span>
                                </div>
                                

                            </div>
                            
                            
                        </form>
                        </div>
                    </div>

                    <div className="separate-box">
                        <div className="line-inner-box"></div>    
                    </div>
                    
                    <form className={classNames({'col s12': true, hiddenText: booking.email === ''})}>
                        <div className="chr_plf10">
                            <div className="row">
                                <div className="col s12 chr_mb9">
                                    <span className={classNames({hiddenText: booking.guestStatus === 'new'})}>
                                        Welcome Back,
                                    </span>
                                    <span className={classNames({hiddenText: booking.guestStatus === 'old'})}>
                                        Welcome,
                                    </span> 
                                </div>
                                <div className="col s12">
                                    <label htmlFor="cFname">Title *</label><br/>
                                    <div className="chr_p10">
                                        <label>
                                            <input className="with-gap" checked={booking.cTitle === 'Mr.'} onChange={(e) => setBooking({...booking, cTitle: e.target.value })} value="Mr." name="group1" type="radio" />
                                            <span>Mr.</span>
                                        </label>
                                        &nbsp;&nbsp;&nbsp;&nbsp;
                                        <label>
                                            <input className="with-gap" checked={booking.cTitle === 'Mrs.'} onChange={(e) => setBooking({...booking, cTitle: e.target.value })} value="Mrs." name="group1" type="radio" />
                                            <span>Mrs.</span>
                                        </label>
                                        &nbsp;&nbsp;&nbsp;&nbsp;
                                        <label>
                                            <input className="with-gap" checked={booking.cTitle === 'Ms.'} onChange={(e) => setBooking({...booking, cTitle: e.target.value })} value="Ms." name="group1" type="radio" />
                                            <span>Ms.</span>
                                        </label>
                                        &nbsp;&nbsp;&nbsp;&nbsp;
                                        <label>
                                            <input className="with-gap" checked={booking.cTitle === 'Dr.'} onChange={(e) => setBooking({...booking, cTitle: e.target.value })} value="Dr." name="group1" type="radio" />
                                            <span>Dr.</span>
                                        </label>
                                    </div>
                                </div>
                                <div className="col l6 s12 chr_mb9">
                                    <label htmlFor="cFname">Firstname *</label>
                                    <input type="text" name="cFname" placeholder="" value={booking.cFname} onChange={(e) => setBooking({...booking, cFname: e.target.value })} required className="validate" />
                                </div>
                                <div className="col l6 s12 chr_mb9">
                                    <label htmlFor="cLname">Lastname *</label>
                                    <input type="text" name="cLname" placeholder="" value={booking.cLname} onChange={(e) => setBooking({...booking, cLname: e.target.value })} required className="validate" />
                                </div>
                                
                                <div className="col l6 s12 chr_mb9">
                                    <label htmlFor="cPhone">Phone Number *</label>
                                    <input type="text" name="cPhone" placeholder="" value={booking.cPhone} onChange={(e) => setBooking({...booking, cPhone: e.target.value })} required className="validate" />
                                    
                                </div>
                                <div className="col l6 s12 chr_mb9">
                                    <label htmlFor="cCountry">Country/region of residence *</label>
                                    <Select 
                                        placeholder='Select Country of Origin'
                                        defaultValue={country.filter(c => c.value === booking.cCountry)}
                                        value={country.filter(c => c.value === booking.cCountry)}
                                        options={country} 
                                        onChange={(e)=>{ return setBooking({...booking, cCountry: e.value }) }}
                                        styles={customStyles}
                                    />
                                    
                                    
                                </div>
                                {/* <div className="col l6 s12 chr_mb9">
                                    <label htmlFor="cNationality">Nationality</label>
                                    <Select 
                                        placeholder='Select Nationality'
                                        defaultValue={country.filter(c => c.value === booking.cNationality)}
                                        value={country.filter(c => c.value === booking.cNationality)}
                                        options={country} 
                                        onChange={(e)=>{ return setBooking({...booking, cNationality: e.value }) }}
                                        styles={customStyles}
                                    />
                                </div> */}
                            </div>
                            <div className="separate-box">
                                <div className="line-inner-box"></div>    
                            </div>
                            <div className="row">
                                

                                <div className="col s12 chr_mb9">
                                
                                    <div className="header-box">
                                        Special Request
                                    </div>
                                </div>  
                                
                                <div className={classNames({'col l6 s12 chr_mb9': true, hiddenText: post.beddingPreff === 0 || post.beddingPreffVal.length < 1})}>
                                    <label htmlFor="cFname">Bedding preff</label><br/>
                                    <div className="chr_p10">
                                        {
                                            post.beddingPreffVal
                                            .map((bedding, index)=>{
                                                return (
                                                    <label key={index}>
                                                        <input className="with-gap" checked={booking.beddingPreff === bedding.id} onChange={(e) => setBooking({...booking, beddingPreff: e.target.value })} value={bedding.id} name="bedding" type="radio" />
                                                        <span>{bedding.title}</span>
                                                    </label>
                                                )
                                                
                                            })
                                        }
                                    </div>
                                </div>    
                                
                                <div className="col l6 s12 chr_mb9">
                                    <label htmlFor="cFname">Smoking</label><br/>
                                    <div className="chr_p10">
                                        <label>
                                            <input className="with-gap" checked={booking.smookOption === 'No Reference'} onChange={(e) => setBooking({...booking, smookOption: e.target.value })} value="No Reference" name="smoking" type="radio" />
                                            <span>No Reference</span>
                                        </label>
                                        &nbsp;&nbsp;&nbsp;&nbsp;
                                        <label>
                                            <input className="with-gap" checked={booking.smookOption === 'No Smoking'} onChange={(e) => setBooking({...booking, smookOption: e.target.value })} value="No Smoking" name="smoking" type="radio" />
                                            <span>No Smoking</span>
                                        </label>
                                        &nbsp;&nbsp;&nbsp;&nbsp;
                                        <label>
                                            <input className="with-gap" checked={booking.smookOption === 'Smoking'} onChange={(e) => setBooking({...booking, smookOption: e.target.value })} value="Smoking" name="smoking" type="radio" />
                                            <span>Smoking</span>
                                        </label>
                                    </div>
                                </div>    
                                <div className="col s12 chr_mb9">
                                    <label htmlFor="comment">Special Request/Comment</label>
                                    <textarea data-length="120" id="comment" className="materialize-textarea validate" value={booking.comment} onChange={(e) => setBooking({...booking, comment: e.target.value })} placeholder="Special Request/Comment"></textarea>
                                    
                                </div>

                                <div className="col s12 chr_mb9">
                                    <label htmlFor="arriveDetail">Optional Information</label>
                                    <textarea data-length="220" id="arriveDetail" className="materialize-textarea validate" value={booking.arriveDetail} onChange={(e) => setBooking({...booking, arriveDetail: e.target.value })} placeholder="Please provide us with your flight number / flight schedule or estimate time arrival when you arrive with sea / land transportation" style={{height: '4rem'}}></textarea>
                                    
                                </div>
                                
                            </div>
                        </div>
                    </form>
                </div>
                
                    
            </div>
        </div>
    );
}

GuestInfo.propTypes = {
    post: PropTypes.object
};
 
export default GuestInfo;