import React, { useEffect, useContext, useRef, useState } from 'react';
import { PromotionContext } from '../../contexts/PromotionContext';
import PromoItems from '../promo-list/PromoItems';
import SummaryAvailablity from '../promo-list/SummaryAvailability';
import AvailabilityLoader from '../promo-list/AvailabilityLoader';
import Banner from '../promo-list/Banner';
import SummaryPromocode from '../promo-list/SummaryPromocode';

const scrollToRef = (ref) => window.scrollTo(0, ref.current.offsetTop-70);

const PromoList = () => {
    
    const { searchPromotion, promotions, updatePromotions, isPromotionLoading, searchParams, infos, apistatus } = useContext(PromotionContext);
    
    const mainRef = useRef(null);

    const didMountRef = useRef(false);

    useEffect(() => {
        if(didMountRef.current){
            scrollToRef(mainRef);
            // console.log('berapa kali kesini 2');
        }else{
            scrollToRef(mainRef);
            // console.log('berapa kali kesini 1');
            didMountRef.current = true;
            const $ = window.$;
            $('.modal').modal();
        }

    },[]);

    // true for ASC and false for DESC
    const [ sortTemp, setSortTemp ] = useState(true);

    const [sortType, setSortType] = useState('rate');

    const handleSortClick = (type) => {
        if(sortType !== type)
            setSortType(type);
        else
            setSortTemp(!sortTemp);
    }

    const sortByRate = (a, b) => {
        if (sortTemp)
            return a - b; 
        else
            return b - a;
    }

    const sortByAlphabet = (a, b) => {
        if (sortTemp){
            if (a < b) { return -1; }
            if (a > b) { return 1; }
        }
        else{
            if (a > b) { return -1; }
            if (a < b) { return 1; }
        }
        return 0;
    }

    const promotionLists = promotions && promotions
        .map((post, index) => {
        return (
            <PromoItems post={post} key={index}/>
        )
    });

    useEffect(() => {
        if(promotions){
            const sortArray = type => {
                const types = {
                    rate: 'rate',
                    room: 'typeName',
                    promo: 'name',
                };
                const sortProperty = types[type];
                
                updatePromotions(promotions.sort((a, b) => {
                    if(sortType === 'rate')
                        return sortByRate(a.rate, b.rate);
                    if(sortType === 'room' || sortType === 'promo')
                        return sortByAlphabet(a[sortProperty], b[sortProperty]);
                    
                    return '';
                }));
            };
        
            sortArray(sortType);
        }
        
    },[sortTemp, promotions, sortType]); // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(() => {

        // if(location.pathname === '/promotions'){
        //     console.log('search form hitted on useEffect!!!!!!!!!!!!!!!');
            searchPromotion();
        // }
            
    },[]);// eslint-disable-line react-hooks/exhaustive-deps

    
    return (                
        (!isPromotionLoading && apistatus !== 'error') ? (
            ( apistatus !== 'gagal' ) ? (
                <div className="container scrollspy chronoz" ref={mainRef} id="main-promo">
                    <Banner infos={infos} />                

                    <SummaryAvailablity infos={infos} jmlPromo={promotions.length} handleSortClick={handleSortClick} sortType={sortType} sortTemp={sortTemp} />

                    <SummaryPromocode infos={infos} />

                    { promotionLists }
                </div>
            ):(
                <div className="container scrollspy chronoz chr_calendar" ref={mainRef} id="main-promo">
                    
                    <SummaryPromocode infos={infos.info} />

                    <div className="row">
                        <div className="col s12">
                            <div className="chr_no_result_availability orange lighten-3">
                                <div dangerouslySetInnerHTML={{__html: infos.apologyText}}></div>        
                            </div>
                        </div>
                    </div>
                    
                </div>
            )
            
        ) : (
            
            (isPromotionLoading) ? (
               
                <AvailabilityLoader searchParams={searchParams} mainRef={mainRef}/>

            ) :(
                <div className="container scrollspy" ref={mainRef} id="main-promo">
                    loading..
                </div>
            )
        )   
        
        
        
    );
}
 
export default PromoList;