import React, { useState, useContext } from 'react';
import PropTypes from 'prop-types';
import { BookingContext } from '../../contexts/BookingContext';
import NumberFormat from 'react-number-format';

const RoomUpgradeDeal = ({post}) => {

    let classNames = require('classnames');

    const { booking, setBooking } = useContext(BookingContext);

    const [hiddenTag] = useState(() => {
        let statusBrfInc = true;
        let statusBrfIncPlus = true;
        let statusBrfNotInc = true;
        if(parseInt(post.breakfast) === 1 && (post.adult-post.maxPax) < 1){
            statusBrfInc = false;
            statusBrfIncPlus = true;
            statusBrfNotInc = true;
        }else if(parseInt(post.breakfast) === 1 && (post.adult-post.maxPax) >= 1){
            statusBrfInc = true;
            statusBrfIncPlus = false;
            statusBrfNotInc = true;
        }else if(parseInt(post.breakfast) === 0)    {
            statusBrfInc = true;
            statusBrfIncPlus = true;
            statusBrfNotInc = false;
        }
        return {
            statusBrfInc : statusBrfInc,
            statusBrfIncPlus : statusBrfIncPlus,
            statusBrfNotInc : statusBrfNotInc
        };
    });

    const addBreakfast = (bQty) => {
        setBooking({...booking, 
            breakfastQty: bQty,
            breakfastRate: post.breakfastRate*post.rooms,
            breakfastTotal: (post.breakfastRate*post.rooms) * bQty
        })
        
    }

    const addExtrabed = (bQty) => {
        setBooking({...booking, 
            extraBedQty: bQty,
            extraBedRate: post.extrabedRate*post.rooms,
            extraBedTotal: (post.extrabedRate*post.rooms) * bQty
        })
        
    }

    return ( 
        <div className="row white z-depth-2">
            <div className="col s12">
                <div className="header-box">
                    Room upgrade deals
                </div>
                
                {/* breakfast */}
                <div className="row chr_mt10" style={{marginBottom: '15px'}}>
                    <div className="col l8 m12">
                        <div className={classNames({hiddenText: hiddenTag.statusBrfInc})}>
                            <i className="material-icons green-text prefix">restaurant</i> &nbsp;&nbsp;&nbsp;
                            Breakfast @ 
                            <NumberFormat value={post.breakfastRate*post.rooms} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            &nbsp; for <b>{post.adult*post.rooms}</b> person
                            <span className="badge included-badge green">Included</span>
                        </div>

                        <div className={classNames({hiddenText: hiddenTag.statusBrfIncPlus && hiddenTag.statusBrfNotInc})}>
                            <i className="material-icons green-text prefix">restaurant</i> &nbsp;&nbsp;&nbsp;
                            Breakfast @ 
                            <NumberFormat value={post.breakfastRate*post.rooms} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            &nbsp; for <b>{booking.breakfastQty}</b> person

                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <span className="chr_btn-active" onClick={()=>{setBooking({...booking, breakfastQty: (booking.breakfastQty >= 1) ? booking.breakfastQty-1 : booking.breakfastQty})}}>
                                <i className={ classNames({ 'grey-text text-lighten-2': booking.breakfastQty < 1,'blue-text text-darken-2':booking.breakfastQty >= 1, 'material-icons':true }) }>
                                    remove_circle_outline
                                </i>
                            </span>
                            <span className={classNames({hiddenText: hiddenTag.statusBrfIncPlus})}>
                                <span className={classNames({disabled: booking.breakfastQty >= (post.adult - post.maxPax), 'chr_btn-active': true })} onClick={()=>{addBreakfast((booking.breakfastQty < (post.adult - post.maxPax)) ? booking.breakfastQty+1 : booking.breakfastQty)} }>
                                    <i className={classNames({'grey-text text-lighten-2': booking.breakfastQty >= (post.adult - post.maxPax),'blue-text text-darken-2': booking.breakfastQty < (post.adult - post.maxPax),'material-icons': true})}>control_point</i>
                                </span>
                            </span>

                            <span className={classNames({hiddenText: hiddenTag.statusBrfNotInc})}>
                                <span className={classNames({disabled: booking.breakfastQty >= post.maxPaxAll, 'chr_btn-active': true })} onClick={()=>{addBreakfast((booking.breakfastQty < post.maxPaxAll) ? booking.breakfastQty+1 : booking.breakfastQty)}}>
                                    <i className={classNames({'grey-text text-lighten-2': booking.breakfastQty >= post.maxPaxAll,'blue-text text-darken-2': booking.breakfastQty < post.maxPaxAll,'material-icons': true})}>control_point</i>
                                </span>
                            </span>
                            
                            <span className={classNames({hiddenText: hiddenTag.statusBrfIncPlus, 'badge included-badge green': true})}>{post.maxPax} person FREE</span>
                        </div>
                        <br/>
                        <div className={classNames({hiddenText: hiddenTag.statusBrfIncPlus, 'orange-text lighten-5': true})}>
                            <i className="material-icons prefix tiny-l">error_outline</i>
                            You have <b>{post.adult - post.maxPax}</b> person who is not included free breakfast recipient
                        </div>
                    </div>
                    <div className="col l4 m12" style={{float:'right'}}>
                        <span className="right">
                            <a onClick={()=>{addBreakfast(booking.breakfastQty+1)} } className={classNames({hiddenText: booking.breakfastQty >= 1 || (hiddenTag.statusBrfIncPlus && hiddenTag.statusBrfNotInc),'waves-effect waves-light btn btn-small animated tada blue lighten-3 valign-wrapper center-align': true })} href="#!"> 
                                Add
                            </a>
                            <a onClick={()=>{setBooking({...booking, breakfastQty: booking.breakfastQty-1})}} className={classNames({hiddenText: booking.breakfastQty < 1,'waves-effect waves-light btn btn-small animated tada blue lighten-3 valign-wrapper center-align': true })} href="#!"> 
                                Remove
                            </a>
                        </span>
                        <div className={classNames({hiddenText: hiddenTag.statusBrfIncPlus && hiddenTag.statusBrfNotInc})}>
                            <span className="upgrade-price">
                                + <NumberFormat value={(post.breakfastRate*post.rooms) * booking.breakfastQty} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            </span>
                            <span className="little-desc">for your entire stay</span>
                        </div>
                        <div className={classNames({hiddenText:  hiddenTag.statusBrfInc })}>
                            <span className="green-text optional-text">
                                FREE
                            </span>
                        </div>
                    </div>
                    
                </div>

                <div className={classNames({row: true, hiddenText: parseInt(post.adult) < parseInt(post.maxPax)})}>
                    <div className="separate-box">
                        <div className="line-inner-box"></div>    
                    </div>
                    
                    <div className="col l8 m12">
                        <div>
                            <i className="material-icons green-text prefix">hotel</i> &nbsp;&nbsp;&nbsp;
                            Extrabed 
                            @
                            <NumberFormat value={post.extrabedRate*post.rooms} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            &nbsp; for <b>{booking.extraBedQty}</b> bed
                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <span className={classNames({disabled: booking.extraBedQty < 1, 'chr_btn-active': true })} onClick={()=>{addExtrabed((booking.extraBedQty >= 1) ? booking.extraBedQty-1 : booking.extraBedQty) }}>
                                <i className={ classNames({ 'grey-text text-lighten-2': booking.extraBedQty < 1,'blue-text text-darken-2':booking.extraBedQty >= 1, 'material-icons':true }) }>
                                    remove_circle_outline
                                </i>
                            </span>
                            <span className={classNames({disabled: booking.extraBedQty >= post.maxAdult, 'chr_btn-active': true })} onClick={()=>{addExtrabed((booking.extraBedQty < post.maxAdult) ? booking.extraBedQty+1 : booking.extraBedQty) }}>
                                <i className={classNames({'grey-text text-lighten-2': booking.extraBedQty >= post.maxAdult,'blue-text text-darken-2': booking.extraBedQty < post.maxAdult,'material-icons': true})}>control_point</i>
                            </span>
                            
                        </div>
                        <br/>
                    </div>
                    <div className="col l4 m12" style={{float:'right'}}>
                        <span className="right">
                            <span onClick={()=>{addExtrabed(booking.extraBedQty+1) }} className={classNames({'hiddenText': booking.extraBedQty >= 1,'waves-effect waves-light btn btn-small animated tada blue lighten-3 valign-wrapper center-align chr_btn-active': true })}> 
                                <span>Add</span>
                            </span>
                            <span onClick={()=>{addExtrabed(booking.extraBedQty-1) }} className={classNames({'hiddenText': booking.extraBedQty < 1,'waves-effect waves-light btn btn-small animated tada blue lighten-3 valign-wrapper center-align chr_btn-active': true })}> 
                                <span>Remove</span>
                            </span>
                        </span>
                        <div>
                            <span className="upgrade-price">
                                + <NumberFormat value={(post.extrabedRate*post.rooms) * booking.extraBedQty} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            </span>
                            <span className="little-desc">for your entire stay</span>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    );
}

RoomUpgradeDeal.propTypes = {
    post: PropTypes.object
};
 
export default RoomUpgradeDeal;