import React, { useState, useContext } from 'react';
import PropTypes from 'prop-types';
import { BookingContext } from '../../contexts/BookingContext';
import { isMobile } from "react-device-detect";
import OptionalServicesItems from './OptionalServicesItems';
import OptionalServicesItemsMobile from './OptionalServicesItemsMobile';

const OptionalServices = ({post}) => {

    let classNames = require('classnames');

    const { booking, setBooking } = useContext(BookingContext);
    
    const [optServicesTemp] = useState(post.services);

    const handleOptServices = (value, type='add') => {
        
        let postSrvc = optServicesTemp.find(item => item.idAdd === value);
        postSrvc.qty = 1;

        if(booking.optServices.length > 0){
            
            let postSrvcUpdate = booking.optServices.find(item => item.idAdd === value);
            if(!postSrvcUpdate){
                // new but not empty
                setBooking({...booking, optServices: [...booking.optServices, postSrvc]})
            } 
            else{
                // update / remove
                if(type === 'remove'){
                    if(postSrvcUpdate.qty === 1){
                        let postSrvcUpdateRemove = booking.optServices.filter(item => item.idAdd !== value);
                        setBooking({...booking, optServices: postSrvcUpdateRemove})
                    }else
                        setBooking({...booking, optServices: booking.optServices.map(el => (el.idAdd === value ? {...el, qty: el.qty-1} : el)) }); 
                }
                    
                else
                    setBooking({...booking, optServices: booking.optServices.map(el => (el.idAdd === value ? {...el, qty: el.qty+1} : el)) }); 
            }   
                
            
        }
        else{
            // new from empty
            setBooking({...booking, optServices: [postSrvc]})
        }
            
    }

    const rateValue = (idAdd, type = 'rate') => {
        
        if(booking.optServices && booking.optServices.length > 0){
            let srvc = booking.optServices.find(item => item.idAdd === idAdd);
            if(typeof srvc !== 'undefined'){

                if(type === 'rate')
                    return srvc.rate*srvc.qty;
                else if(type === 'qty')
                    return srvc.qty;
                
            }
                
        }
        return 0;
    }

    return ( 
        <div className={classNames({hiddenText: post.services.length < 1, 'row white z-depth-2 mobile': true})}>
            <div className="col s12">
                <div className="header-box">
                    Optional Services
                </div>
                
                
                    {
                        post.services && post.services
                            .map((srvc, index) => {
                            return (
                                <div key={index}>
                                    <div className={classNames({hiddenText: isMobile})}>
                                        <OptionalServicesItems post={post} srvc={srvc} rateValue={rateValue} handleOptServices={handleOptServices} />
                                    </div>
                                    <div className={classNames({hiddenText: !isMobile})}>
                                        <OptionalServicesItemsMobile post={post} srvc={srvc} rateValue={rateValue} handleOptServices={handleOptServices} />
                                    </div>
                                </div>
                            )
                        })
                    }
                    
            </div>
        </div>
    );
}

OptionalServices.propTypes = {
    post: PropTypes.object
};
 
export default OptionalServices;