export function PercentRate(oldRate, newRate) {
    let persentase = ((oldRate - newRate) / oldRate) *100;
        persentase = persentase.toFixed(2);
    
    return persentase;
}

export function PluralSentence(number, text){
    let result = text;
    if(number > 1)
        result += "s";
    
    return result;
}

export function GlobalPajak (srvc, govTax, total_value){
    let srvcCharge = (total_value * srvc) / 100;
    let ppn = ( (total_value + srvcCharge) * govTax) / 100;
    let pajak = srvcCharge + ppn;
    return pajak;
}

export function GetTotal(booking, post){
    let total = 0;
    let services = booking.optServices;
    if(services.length > 0){
        services.map(item => {
            if(item.qty)
                total += item.qty * item.rate;
            
            return '';
        })
    }
    
    total += (booking.breakfastQty > 0)? booking.breakfastTotal:0; // Adult breakfast
    total += (booking.extraBedQty > 0)? booking.extraBedTotal:0; // Extrabed
    total += post.rooms*post.rate;
    if(parseInt(post.taxIncluded) === 0) // for ++ price
        total += GlobalPajak(parseInt(post.srvcTax), parseInt(post.govTax), total);
    
    return total;
}

export function DepositGuarantee(option, value, deposit, total, post){
        
    if(option === '0'){ // for day
        // guarantee = value * deposit;
        //new guarantee day
        let daynya = 0;
        let guarantee = 0;
        let allotments = post.allotments;

        allotments.map(item => {
            if(daynya < deposit){
                guarantee += item.rate;
                daynya++;
            }

            return '';
        })
        
        return guarantee;
    }
    else if(option === '1'){ // for %
        let guarantee = (total * deposit) / 100;
        return guarantee;
    }
}

export function TaxInclude(value, post){
    let includeTax = GlobalPajak(parseInt(post.srvcTax), parseInt(post.govTax), value);
    let total = 0;
    if(post.taxIncluded === 0) // for ++ price
        total = value+includeTax;
    else
        total = value;

    return total;
}

export function ExpandIcon(value){
    if(value)
        return 'expand_more';
    else
        return 'expand_less';
}

export function DigitAfterComa(value, number){
    
    return value.toLocaleString(undefined, {maximumFractionDigits:number});
}

