import React, { useState } from 'react';
import { PluralSentence } from '../Helper';

const LastSearch = () => {

    let classNames = require('classnames');

    const [ lastSearchParams ] = useState(() => {
        const localData = localStorage.getItem('last-search-params');
        return localData ? JSON.parse(localData) : {}
    });

    return (  
        <div className={classNames({'last-search': true, hiddenText: typeof lastSearchParams.startDate === 'undefined'})}>
            Your last Search: <br />
            
            {lastSearchParams.startDate+" - "+lastSearchParams.endDate+" | "+ lastSearchParams.pax +" "+PluralSentence(lastSearchParams.pax, "Room")+" "+lastSearchParams.adu+" "+PluralSentence(lastSearchParams.adu, "Adult")}
        </div>
    );
}
 
export default LastSearch;