import React, { useContext } from 'react';
import { Link } from "react-router-dom";
import PropTypes from 'prop-types';
import { BookingContext } from '../../contexts/BookingContext';

const ProceedButton = ({post}) => {

    let classNames = require('classnames');

    const { booking, setBooking, proceedToPayment } = useContext(BookingContext);

    const handleSubmitData = (e) => {
        e.preventDefault();
        console.log("data booking",booking);
        proceedToPayment(post);
    }
    return (  
        <div>
            <div className="separate-box">
                <div className="line-inner-box"></div>    
            </div>

            <div className="col s12">
                <form className="">
                    <div className="chr_mtb10">
                        <label>
                            <input onChange={(e)=>setBooking({...booking, aggrement: e.target.checked}) } type="checkbox" />
                            <span>
                                I agree to hotels
                                <a href="#!"> Terms of Use and Privacy Policy</a>
                            </span>
                        </label>
                    </div>
                </form>
            </div>

            {/* <div className="row white"> */}
            <div className="col l6 offset-l6 s12">
                <div className="right chr_mtb10">
                    <div className={ classNames({ hiddenText: post.depositText === '', chr_mb9:true }) } >
                        <p className="chr_label green">
                            <i className="material-icons tiny-s perfix">local_offer</i>&nbsp;&nbsp;
                            {post.depositText}
                        </p>
                    </div> 
                </div>
            </div>
            <div className="col l6 offset-l6 s12">
                <div className="right chr_mtb10">
                    <div>
                        <a className={ classNames({ 'hiddenText': post.hid.refundable === '1', 'tooltipped grey-text': true }) } data-tooltip={post.hid.refundableTextDesc} href="#!" >
                            <i className="fa fa-info-circle"></i>&nbsp;
                            {post.hid.refundableText}
                        </a>
                        <a className={ classNames({ 'hiddenText': post.hid.refundable !== '1', 'tooltipped green-text': true }) } data-tooltip={post.hid.refundableTextDesc} href="#!" >
                            <i className="fa fa-info-circle"></i>&nbsp;
                            {post.hid.refundableText}
                        </a>
                    </div>
                </div>
                
            </div>
            
            <div className="col l8 offset-l4 s12">
                
                <div className="right chr_mtb10">
                    <Link className={ classNames({ 'right waves-effect waves-light btn btn-large animated tada grey darken-2 valign-wrapper center-align': true }) } to={'/promotions'} style={{marginRight: '10px'}}>
                        Back
                    </Link>
                    <span onClick={handleSubmitData} className={classNames({disabled: (booking.aggrement === false || booking.paymentOption === null || booking.cTitle ==='' || booking.cFname === '' || booking.cLname === '' || booking.cPhone === '' || booking.cCountry === ''),'right waves-effect waves-light btn btn-large animated tada blue darken-3 valign-wrapper center-align chr_btn-active': true })}> 
                        Proceed to Payment
                    </span>
                </div>
                
            </div>

            <div className={classNames({'col s12': true, hiddenText: (booking.aggrement === false || booking.paymentOption === null || booking.cTitle ==='' || booking.cFname === '' || booking.cLname === '' || booking.cPhone === '' || booking.cCountry === '') === false })}>
                
                <div className="right chr_alert-danger">
                    <p> 
                        * Please check fields required above and agreed terms and conditions
                    </p>
                </div>
                
            </div>
            {/* </div> */}
        </div>
    );
}

ProceedButton.propTypes = {
    post: PropTypes.object,
    proceedToPayment: PropTypes.func
};

export default ProceedButton;