import React from 'react';
import Footer from './components/sub-page/Footer';
import './App.css';

import PromotionContextProvider from './contexts/PromotionContext';
import BookingContextProvider from './contexts/BookingContext';

import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import Home from './components/page/Home';
import Promotions from './components/page/Promotions';
import Reservation from './components/page/Reservation';

function App() {

  return (
    <Router>
      <div className="App">
        <PromotionContextProvider>
          
          <Switch>
            <Route exact path="/" component={Home} />
            <Route exact path="/promotions" component={Promotions} />
            <BookingContextProvider>
              <Route path="/reservation/:post_id" component={Reservation} />
            </BookingContextProvider>
          </Switch>

        </PromotionContextProvider>
        
        
        <Footer />
      </div>
    </Router>
  );
}

export default App;
