import React, { useState, useContext } from 'react';
import { PromotionContext } from '../../contexts/PromotionContext';
import moment from 'moment';
import InfiniteCalendar, { withRange, Calendar} from 'react-infinite-calendar';
import 'react-infinite-calendar/styles.css';
import LastSearch from '../sub-page/LastSearch';
import { Redirect } from "react-router-dom";

const FormSearchMobile = () => {

    let classNames = require('classnames');

    const { searchParams, setSearchParams, searchPromotion } = useContext(PromotionContext);

    let tomorrow  = moment(new Date()).add(1,'days');
    let afterTomorrow  = moment(new Date()).add(2,'days');

    const changeDate = (dataDate) => {
        console.log(dataDate);
        console.log(moment(dataDate.start) ? moment(dataDate.start).format("D") : tomorrow.format("D"));

        setSearchParams({
            ...searchParams,
            startDate: dataDate.start ? moment(dataDate.start).format('ddd, MMM DD YYYY') : tomorrow.format('ddd, MMM DD YYYY'), 
            endDate: dataDate.end ? moment(dataDate.end).format('ddd, MMM DD YYYY') : afterTomorrow.format('ddd, MMM DD YYYY'), 
            fd:moment(dataDate.start) ? moment(dataDate.start).format("D") : tomorrow.format("D"),
            fm:moment(dataDate.start) ? moment(dataDate.start).format("M") : tomorrow.format("M"),
            fy:moment(dataDate.start) ? moment(dataDate.start).format("YYYY") : tomorrow.format("YYYY"),
            td:moment(dataDate.end) ? moment(dataDate.end).format("D") : afterTomorrow.format("D"),
            tm:moment(dataDate.end) ? moment(dataDate.end).format("M") : afterTomorrow.format("M"),
            ty:moment(dataDate.end) ? moment(dataDate.end).format("YYYY") : afterTomorrow.format("YYYY"),
        });
    }

    const [toPromo, setToPromo] = useState(false);

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(searchParams);
        searchPromotion();
        setToPromo(true);
    }
    
    const [ datePickerIsOpen, isDatePickerIsOpen ] = useState(false);
    const openDatePicker = () => {
        isDatePickerIsOpen(!datePickerIsOpen);
    }
    
    const [ optionalPickerOpen, isOptionalPickerOpen ] = useState(false);
    const optionalPicker = () => {
        isOptionalPickerOpen(!optionalPickerOpen);
        console.log('ini ada', optionalPickerOpen);
    }

    const [state] = useState({
        start: searchParams.startDate,
        end: searchParams.endDate  
        // start: new Date(tomorrow),
        // end: new Date(afterTomorrow)  
    });

    const getNight = () => {
        let dateStart= moment(searchParams.startDate);
        let dateEnd= moment(searchParams.endDate);
        let diff = dateEnd.diff(dateStart);
        let diffDuration = moment.duration(diff);
        return "( "+ diffDuration.asDays() + " Nights )";
    }

    return (!toPromo) ? (  
        <div className="chr-form mobile">
            <div className={ classNames({ hiddenText: !datePickerIsOpen }) }>
                <div className="modal-dialogs">
                    <InfiniteCalendar
                    Component={withRange(Calendar)}
                    selected={state}
                    width='100%'
                    onSelect={(selectedDate)=>selectedDate.eventType !== 1 ? changeDate(selectedDate) : ''}
                    />
                    <a href="#!" onClick={() => {isDatePickerIsOpen(!datePickerIsOpen);}} className="waves-effect waves-light btn btn-large btn-block calendar-ok-btn orange darken-3">
                        OK {getNight()}
                    </a>

                </div>
            </div>
            <div className={ classNames({ hiddenText: !optionalPickerOpen }) }>
                <div className="modal-dialogs">
                    <a href="#!" onClick={() => {isOptionalPickerOpen(!optionalPickerOpen);}} className="left-align waves-effect waves-light btn btn-large btn-block btn-back blue darken-2">
                        <i className="material-icons white-text left">chevron_left</i> 
                        Occupancy
                    </a>

                    <div className="optional-modal white" height="100%">
                        <ul className="collection with-header">
                            <li className="collection-item">
                                <div>
                                    <span className="form-title blue-text text-darken-2"> {searchParams.pax} </span>
                                    Room
                                    <a href="#!" className="secondary-content" onClick={()=>{setSearchParams({...searchParams, pax: searchParams.pax+1})}}><i className="material-icons blue-text text-darken-2">control_point</i></a>
                                    <a href="#!" className="secondary-content" onClick={()=>{setSearchParams({...searchParams, pax: (searchParams.pax > 1) ? searchParams.pax-1 : searchParams.pax})}}><i className={ classNames({ 'grey-text text-lighten-2': searchParams.pax < 2,'blue-text text-darken-2':searchParams.pax > 1, 'material-icons':true }) }>remove_circle_outline</i></a>
                                </div>
                            </li>
                            <li className="collection-item">
                                <div>
                                    <span className="form-title blue-text text-darken-2"> {searchParams.adu} </span>
                                    Adult
                                    <a href="#!" onClick={()=>{setSearchParams({...searchParams, adu: searchParams.adu+1})}} className="secondary-content"><i className="material-icons blue-text text-darken-2">control_point</i></a>
                                    <a href="#!" className="secondary-content" onClick={()=>{setSearchParams({...searchParams, adu: (searchParams.adu > 1) ? searchParams.adu-1 : searchParams.adu})}}><i className={ classNames({ 'grey-text text-lighten-2': searchParams.adu < 2,'blue-text text-darken-2':searchParams.adu > 1, 'material-icons':true }) }>remove_circle_outline</i></a>
                                </div>
                            </li>
                            <li className="collection-item">
                                <div>
                                    <span className="form-title blue-text text-darken-2"> {searchParams.chi} </span>
                                    Children
                                    <a href="#!" onClick={()=>{setSearchParams({...searchParams, chi: searchParams.chi+1})}} className="secondary-content"><i className="material-icons blue-text text-darken-2">control_point</i></a>
                                    <a href="#!" className="secondary-content" onClick={()=>{setSearchParams({...searchParams, chi: (searchParams.chi > 0) ? searchParams.chi-1 : searchParams.chi})}}><i className={ classNames({ 'grey-text text-lighten-2': searchParams.chi < 1,'blue-text text-darken-2':searchParams.chi > 0, 'material-icons':true }) }>remove_circle_outline</i></a>
                                </div>
                            </li>
                            {/* <li className="collection-item"><div>Promocode<a href="#!" className="secondary-content"><i className="material-icons">control_point</i></a></div></li> */}
                        </ul>
                    </div>
                    
                    <a href="#!" onClick={() => {
                        isOptionalPickerOpen(!optionalPickerOpen);
                        }} className="waves-effect waves-light btn btn-large btn-block calendar-ok-btn orange darken-3">OK</a>

                </div>
            </div>
          
            <form onSubmit={handleSubmit}>
                <div className="row z-depth-4 white center" onClick={openDatePicker}>
                        
                        <div className="col s6" style={{padding:"0px"}}>
                            <div className="checkinout-date">
                                <span className="grey-text">Check in</span>
                                <span className="date blue-text text-darken-2">{moment(searchParams.startDate).format('DD')}</span>
                                <span>{moment(searchParams.startDate).format('ddd, MMM YYYY')}</span>
                            </div>
                        </div>
                        <div className="col s6" style={{padding:"0px"}}>
                            <div className="checkinout-date">
                                <span className="grey-text">Check out</span>
                                <span className="date blue-text text-darken-2">{moment(searchParams.endDate).format('DD')}</span>
                                <span>{moment(searchParams.endDate).format('ddd, MMM YYYY')}</span>
                            </div>
                        </div>
                    
                </div>
                    
                <div className="row white z-depth-4 center" onClick={optionalPicker}>
                    <div className="col s12" style={{padding:"0px"}}>
                            <div className=" checkinout-date">
                                <span className="form-title blue-text text-darken-2"> {searchParams.pax} </span>
                                    Rooms 
                                <span className="form-title blue-text text-darken-2"> {searchParams.adu} </span>
                                    Adult 
                                <span className="form-title blue-text text-darken-2"> {searchParams.chi} </span>
                                    Children
                            </div>
                    </div>
                </div>
                <div className="row white z-depth-4">
                    <div className="col s12" style={{padding:"0px"}}>
                        <a className="waves-effect waves-light btn btn-large animated tada orange darken-3 valign-wrapper center-align btn-search" href="#main-promo" onClick={handleSubmit}> Submit </a>
                    </div>
                </div>

                <div className="row white z-depth-4">
                    <div className="col s12" style={{padding:"0px"}}>
                        <LastSearch />
                    </div>
                </div>
              
            </form>    
        </div>
        
    ):(
        <Redirect to='/promotions' />
    );
}
 
export default FormSearchMobile;