import React from 'react';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';
import { ExpandIcon } from '../Helper';

const SummaryAvailablity = ({infos, jmlPromo, handleSortClick, sortType, sortTemp}) => {

    let classNames = require('classnames');

    return (  
        <div className="chr_summary_availability">
            <div className="row" style={{marginBottom: '0px'}}>
                <div className="col l7 m12" style={{padding: '0 5px'}}>
                    <p>
                        <span>There are <b>{infos.rooms}</b> unit available </span>
                        <span className={classNames({hiddenText: jmlPromo <= 1}) }> with various promotions </span>
                        <span>start from </span>
                        <span>
                            <b>
                                <NumberFormat value={infos.rateStartnya} displayType={'text'} thousandSeparator={true} prefix={infos.cur+ " "} />
                            </b>
                        </span>
                        <span> / unit / night</span>	
                    </p>
                </div>
                <div className="col l5 m12 right" style={{padding: '5px'}}>
                    <div className="chr_btn_sorting">
                        <a className={ classNames({'waves-effect waves-light btn-small btn-flat white-text blue': true, 'darken-4': sortType === 'promo'}) } href="#!" onClick={() => {handleSortClick('promo')}}>
                            <i className={ classNames({'material-icons right': true, hiddenText: sortType !== 'promo'}) } >
                                {ExpandIcon(sortTemp)}
                            </i>
                            PROMO
                        </a>
                        <a className={ classNames({'waves-effect waves-light btn-small btn-flat white-text blue': true, 'darken-4': sortType === 'room'}) } href="#!" onClick={() => {handleSortClick('room')}}>
                            <i className={ classNames({'material-icons right': true, hiddenText: sortType !== 'room'}) } >
                                {ExpandIcon(sortTemp)}
                            </i>
                            ROOM
                        </a>
                        <a className={ classNames({'waves-effect waves-light btn-small btn-flat white-text blue': true, 'darken-2': sortType === 'rate'}) } href="#!" onClick={() => {handleSortClick('rate')}}>
                            <i className={ classNames({'material-icons right': true, hiddenText: sortType !== 'rate'}) } >
                                {ExpandIcon(sortTemp)}
                            </i>
                            PRICE
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
}

SummaryAvailablity.propTypes = {
    infos: PropTypes.object,
    jmlPromo: PropTypes.number,
    handleSortClick: PropTypes.func,
    sortType: PropTypes.string,
    sortTemp: PropTypes.bool
};
 
export default SummaryAvailablity;