import React, { useState, useEffect } from 'react';
import createActivityDetector from 'activity-detector';
import { Link } from "react-router-dom";
import M from "materialize-css";
// import { Modal, Button } from 'react-materialize';

const TimeoutPage = () => {
  const [seconds, setSeconds] = useState(0);
  const [minutes, setMinutes] = useState(1);
  const [isActive] = useState(true);

  const [isIdle, setIsIdle] = useState(false);

  useEffect(() => {    
      let activityDetector = createActivityDetector({timeToIdle: 10000});
      activityDetector.on('idle', () => setIsIdle(true))
      activityDetector.on('active', () => setIsIdle(false))
      return () => {
          activityDetector.stop()
      }
  }, [])

  // const toggle = () => {
  //   setIsActive(!isActive);
  // }

  // const reset = () => {
  //   setSeconds(0);
  //   setIsActive(false);
  // }

  useEffect(() => {
    let interval = null;
    if (isActive) {
      interval = setInterval(() => {
        
            if (seconds > 0) {
                setSeconds(seconds - 1);
            }
            if (seconds === 0) {
                if (minutes === 0) {
                    clearInterval(interval);
                    let elems = document.querySelector('.modal');
                    let instance = M.Modal.init(elems, '');
                    instance.open();
                    // window.location.reload();
                } else {
                    setSeconds(59);
                    setMinutes(minutes - 1);
                }
            } 

      }, 1000);

      

    } else if (!isActive && seconds !== 0) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);

  }, [isActive, seconds]); // eslint-disable-line react-hooks/exhaustive-deps

  const timeoutpageStyle = {
    fontSize: '115%',
    color: '#1566d1',
    fontWeight: '600', 
  }

  return (
    <div className="">
        <div className="row white z-depth-2 blue lighten-5">
            <div className="col l6 offset-l6 s12">
                <div className="right chr_p10">
                    <span>{isIdle ? 'Welcome back!' : 'Keep your price...'}</span> &nbsp;&nbsp;
                    
                    <span style={timeoutpageStyle}>
                        <i className="material-icons">access_time</i> &nbsp;
                        { 
                          <span>{'00:0' + minutes}:{seconds < 10 ? '0' + seconds : seconds} </span>
                        }
                    </span>
                </div>
                    
            </div>
        </div>

        <div id={'modal-timeoutpage'} className="modal">
        
            <div className="modal-content">
                <div className="row">
                    <div className="col l12 s12">

                        <div className="desc-content">
                            <div className="desc-content-header">
                                <h6>Welcome Back !!!</h6>
                            </div>	

                            <div className="panel-body-custom">
                              <h6>Price and availability may have changed while you were gone</h6>
                            </div>	

                            <Link className="waves-effect waves-light btn btn-large animated tada blue darken-3 valign-wrapper center-align btn-chrbook" to={'/promotions'}>
                              <i className="material-icons">autorenew</i> &nbsp;
                              Refresh prices and availability
                            </Link>
                        </div>

                    </div>
                </div>
                
            </div>
            
        </div>
      
    </div>
  );
};

export default TimeoutPage;