import React, { createContext, useState } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import qs from 'qs';
import { TaxInclude, GetTotal, DepositGuarantee } from '../components/Helper';

export const BookingContext = createContext();

const BookingContextProvider = (props) => {

    const [booking, setBooking] = useState({
        paymentOption: null,
        extraBedQty: 0,
        extraBedRate: 0, // amount per unit extrabed
        extraBedTotal: 0, // total amount extreabed
        breakfastQty: 0,
        breakfastRate: 0, // amount per unit breakfast
        breakfastTotal: 0, // total amount breakfast
        optServices:{},
        email: '',
        api: 'e972a19b09ea90cf3ff34ec12359bd7b1',
        cTitle : '',
        cFname : '',
        cLname : '',
        cPhone : '',
        cMobile : '',
        cEmail : '',
        cAddress : '',
        cCity : '',
        cState : '',
        cZipcode : '',
        cCountry : '',
        cPassport : '',
        cNationality : '',
        guestStatus: 'new',
        aggrement: false
    });

    const changePaymentOption = (value) => {
        setBooking({...booking, paymentOption: value });
        console.log('data booking: ', booking);
    }

    const searchUserEmail = () => {
        axios.get('https://api.ichronoz.net/availability/guest/10?&email='+booking.email+'&api='+booking.api)
            .then(res => {
                
                // console.log(res.data);
                let mystring = res.data;
                mystring = mystring.replace('(','');
                mystring = mystring.replace(')','');
                mystring = JSON.parse(mystring);
                setBooking({...booking, 
                    cTitle : mystring.guest.gtitle,
					cFname : mystring.guest.gfirst_name,
					cLname : mystring.guest.glast_name,
					cPhone : mystring.guest.gphone,
					cMobile : mystring.guest.gmobile,
					cEmail : mystring.guest.gemail,
					cAddress : mystring.guest.gaddress,
					cCity : mystring.guest.gcity,
					cState : mystring.guest.gstate,
					cZipcode : mystring.guest.gzipcode,
					cCountry : mystring.guest.gcountry,
					cPassport : mystring.guest.gpassport,
                    cNationality : mystring.guest.gnationality,
                    guestStatus: 'old',
                })
                
            })
            .catch(error => {
                setBooking({...booking, 
                    cTitle : '',
					cFname : '',
					cLname : '',
					cPhone : '',
					cMobile : '',
					cEmail : booking.email,
					cAddress : '',
					cCity : '',
					cState : '',
					cZipcode : '',
					cCountry : '',
					cPassport : '',
                    cNationality : '',
                    guestStatus: 'new',
                })
                console.log('thank you',error);
                // alert('Sorry! we are under maintenance!!!', error);
            });
    }

    const proceedToPayment = (post) => {
        let rateTax = TaxInclude(post.rate, post);
        let roomRates = {
            items: []
        };
        //for Room Rates
        for (var key in post.allotments) {
            roomRates.items.push({
                date: post.allotments[key].dates,
                oldRate: TaxInclude(post.allotments[key].oldRate, post),
                rate: TaxInclude(post.allotments[key].rate, post),
            });
        }

        let addServices = {
            items: []
        }

        //for room
        addServices.items.push({
            id: post.id,
            name: post.name+' - '+post.typeName+' '+post.rooms+' Room x '+post.night+' Night',
            price: TaxInclude(post.rooms*post.rate, post),
            qty: 1,
            amount: TaxInclude(post.rooms*post.rate, post),
            type: 1,// type 1 = other / except services
        });

        //for breakfast booking.breakfastQty > 0
        if(booking.breakfastQty > 0){
            addServices.items.push({
                id: post.id,
                name: 'Breakfast for '+(post.adult * parseInt(post.roomPerVilla))*post.rooms+' Adult x '+post.night+' Night',
                price: TaxInclude(booking.breakfastTotal, post),
                qty: 1,
                amount: TaxInclude(booking.breakfastTotal, post),
                type: 1,// type 1 = other / except services
            });
        }

        //for extrabed booking.extraBedQty > 1
        if(booking.extraBedQty > 0){
            addServices.items.push({
                id: post.id,
                name: 'Extrabed for '+(booking.extraBedQty)+' Bed x '+post.night+' Night',
                price: TaxInclude(booking.extraBedTotal, post),
                qty: 1,
                amount: TaxInclude(booking.extraBedTotal, post),
                type: 1,// type 1 = other / except services
            });
        }
        
        //for additional services
        booking.optServices
        .map((srvcItem) => {
            addServices.items.push({
                id: srvcItem.idAdd,
                name: srvcItem.name,
                price: TaxInclude(srvcItem.qty*srvcItem.rate, post),
                qty: srvcItem.qty,
                amount: TaxInclude(srvcItem.qty*srvcItem.rate, post),
                type: 0,// type 0 = services
            });

            return '';
        })
        // for (var srvcItem in booking.optServices) {
        //     addServices.items.push({
        //         id: srvcItem.idAdd,
        //         name: srvcItem.name,
        //         price: TaxInclude(srvcItem.qty*srvcItem.rate, post),
        //         qty: srvcItem.qty,
        //         amount: TaxInclude(srvcItem.qty*srvcItem.rate, post),
        //         type: 0,// type 0 = services
        //     });
        // }
        
            // guarantee booking deposit
        let totalGuarantee = DepositGuarantee(post.guaranteeOpt, TaxInclude((post.rate/post.night)*post.rooms, post ), post.guarantee, GetTotal(booking, post), post );
        
        //get total
        let totalAmount = GetTotal(booking, post);
            
        const dataBooking = {
            hid: post.hid, roomId: post.id, roomName: post.name, promoType: post.promoType, 
            rate: rateTax, night: post.night, roomDesc: post.desc,
            rooms: post.rooms, promoDate: post.promoDate, cutOff: post.cutOfDay,
            lastMinDeal: post.lastMinute, condition: post.condition,
            govTax: post.govTax, srvcTax: post.srvcTax, 
            arrivalDate: post.fromdate, departureDate: post.todate, 
            paymentOption:booking.paymentOption, 
            total: totalAmount,
            roomRate: roomRates, 
            currency: post.cur, adult: post.adult, child: post.child, 
            breakfastInclude: post.breakfast,
            roomPerVilla: post.roomPerVilla,
            exchangeRate: 1000,//$rootScope.exchange,
            surchargeDescription: post.surchargeDescription,
            
            // for guarantee booking
            totalGuarantee: totalGuarantee,
            totalRest: totalAmount - totalGuarantee,
            
            //optional breakfast
            breakfast:booking.breakfastQty, 
            breakfastCharge: booking.breakfastTotal,
            
            //optional extrabed
            extrabed:booking.extraBedQty, 
            extrabedCharge: booking.extraBedTotal,
            
            //additional services
            addSrvc: addServices,
            
            // guest information
            cTitle : booking.cTitle,cFname : booking.cFname, cLname : booking.cLname, cPhone : booking.cPhone,
            cMobile : booking.cMobile, cAddress : booking.cAddress, cCity: booking.cCity, cState: booking.cState,
            cZipcode: booking.cZipcode, cCountry: booking.cCountry, cEmail : booking.cEmail, 
            cNationality: booking.cNationality, cPassport: booking.cPassport,
            arriveBy : booking.arriveBy,arriveDetail : booking.arriveDetail, 
            smook: booking.smookOption, comment: booking.comment, 
            mailingList: booking.mailingList, 
            beddingPreff: booking.beddingPreff, 
            //apikey
            id : 13, url: '', api: booking.api, utc: '', model: 'posts',
            callback: "JSON_CALLBACK"
          };
        console.log('data bookingan: ',dataBooking);
        // axios.post('https://api.ichronoz.net/availability/create/posts', qs.stringify(dataBooking), {
        //     headers: {'Content-Type': 'application/x-www-form-urlencoded'} 
        // })
        axios({
            method: 'post',
            url: 'https://api.ichronoz.net/availabilityV3/create/posts',
            data: qs.stringify(dataBooking),
            headers: {
              'content-type': 'application/x-www-form-urlencoded'
            }
          })
            .then(res => {
                console.log(res);
                console.log(res.data);
                if(res != null){
                    window.location = res.data;
                }else{
                    // $window.location.href = $window.document.location.origin;
                    window.location = window.document.location.origin;
                    
                }
                
            })
            .catch(error => {
                
                console.log('thank you',error);
            });
        
    }
    
    return (  
        <BookingContext.Provider value={{ booking, changePaymentOption, setBooking, searchUserEmail, proceedToPayment }}>
            {props.children}
        </BookingContext.Provider>
    );
}

BookingContextProvider.propTypes = {
    children: PropTypes.node,
};

export default BookingContextProvider;