import React, { useState } from 'react';
import ImagePartMobile from './ImagePartMobile';
import DescPartMobile from './DescPartMobile';
import RatePartMobile from './RatePartMobile';
import PropTypes from 'prop-types';

const PromoItemsMobile = ({post}) => {

    let classNames = require('classnames');
    
    const [ promoTermsIsOpen, isPromoTermsIsOpen ] = useState(false);

    const openPromoTerms = () => {
        isPromoTermsIsOpen(!promoTermsIsOpen);
    }

    const [ promoDetailIsOpen, isPromoDetailIsOpen ] = useState(false);

    const openPromoDetail = () => {
        isPromoDetailIsOpen(!promoDetailIsOpen);
    }

    const [ showAll, isShowAll ] = useState(false);

    const showAllContent = () => {
        isShowAll(!showAll);
    }
    return (  
        <div className="card"> 
            <div className="card-image">

                <ImagePartMobile post={post} promoDetailIsOpen={promoDetailIsOpen} openPromoDetail={openPromoDetail} showAll={showAll} promoTermsIsOpen={promoTermsIsOpen} openPromoTerms={openPromoTerms} />
                
            </div>
            <div className="card-content">
                <div className="row" >
                    <div className="col l5 s12 chr_mb9">
                        
                        <DescPartMobile post={post} promoDetailIsOpen={promoDetailIsOpen} openPromoDetail={openPromoDetail} promoTermsIsOpen={promoTermsIsOpen} openPromoTerms={openPromoTerms} showAllContent={showAllContent} showAll={showAll} />
                        
                    </div>
                    <div className={ classNames({hiddenText: !showAll, 'col l3 s12 text-right': true}) }>
                        
                        <RatePartMobile post={post} showAll={showAll} />

                    </div>
                </div>

            </div> 
            <div className="card-action blue-text center" onClick={showAllContent}>
                <span className={ classNames({hiddenText: showAll})}>Show More Promo Description <i className="material-icons">keyboard_arrow_down</i></span>
                <span className={ classNames({hiddenText: !showAll})}>Less <i className="material-icons">keyboard_arrow_up</i></span>
            </div>
        </div>
    );
}

PromoItemsMobile.propTypes = {
    post: PropTypes.object,
};

export default PromoItemsMobile;