import React, { useState, useContext } from 'react';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';
import { PercentRate } from '../Helper';
import { BookingContext } from '../../contexts/BookingContext';

const ReservationRateSummary = ({post}) => {

    let classNames = require('classnames');

    const { booking } = useContext(BookingContext);

    const onePajak = (srvc, govTax, total_value, type) => {
        let srvcCharge = (total_value * srvc) / 100;
        let ppn = ( (total_value + srvcCharge) * govTax) / 100;
        if(type === 'srvc_charge')
            return srvcCharge;
        else if(type === 'gov_tax')
            return ppn;
    };

    const getTotalBeforeTax = () => {
        let total = 0;
        let services = booking.optServices;
        if(services.length > 0){
            services.map(item => {
                if(item.qty)
                    total += item.qty * item.rate;

                return '';
            })
        }
        
        // total += (post.breakfast !== 1 && post.adult > 0 && booking.breakfastQty > 0)? (post.adult*post.breakfastRate)*post.rooms:0; // Adult breakfast
        total += (booking.breakfastQty > 0)? booking.breakfastTotal:0; // Adult breakfast
        // total += (post.jumlah > 0)? ((post.jumlah * post.extrabedRate ) * post.rooms):0; // Extrabed
        total += (booking.extraBedQty > 0)? booking.extraBedTotal:0; // Extrabed
        total += post.rooms*post.rate;

        return total;
    }

    const globalPajak = (srvc, govTax, total_value) => {
        let srvcCharge = (total_value * srvc) / 100;
        let ppn = ( (total_value + srvcCharge) * govTax) / 100;
        let pajak = srvcCharge + ppn;
        return pajak;
    };

    const getTotal = () => {
        let total = 0;
        let services = booking.optServices;
        if(services.length > 0){
            services.map(item => {
                if(item.qty)
                    total += item.qty * item.rate;
                
                return '';
            })
        }
        
        // total += (post.breakfast !== '1' && post.adult > 0 && booking.breakfastQty > 0)? (post.adult*post.breakfastRate)*post.rooms:0; // Adult breakfast
        total += (booking.breakfastQty > 0)? booking.breakfastTotal:0; // Adult breakfast
        // total += (post.jumlah > 0)? ((post.jumlah * post.extrabedRate ) * post.rooms):0; // Extrabed
        total += (booking.extraBedQty > 0)? booking.extraBedTotal:0; // Extrabed
        total += post.rooms*post.rate;
        if(parseInt(post.taxIncluded) === 0) // for ++ price
            total += globalPajak(parseInt(post.srvcTax), parseInt(post.govTax), total);
            // total += $scope.getTax(post.srvcTax)+$scope.getTax(post.govTax);

        return total;
    }

    const depositGuarantee = (option, value, deposit, total, post) => {
        
        if(option === '0'){ // for day
            // guarantee = value * deposit;
            //new guarantee day
            let daynya = 0;
            let guarantee = 0;
            let allotments = post.allotments;

            allotments.map(item => {
                if(daynya < deposit){
                    guarantee += item.rate;
                    daynya++;
                }

                return '';
            })
            
            return guarantee;
        }
        else if(option === '1'){ // for %
            let guarantee = (total * deposit) / 100;
            return guarantee;
        }
    };

    const taxInclude = (value) => {
        let includeTax = globalPajak(parseInt(post.srvcTax), parseInt(post.govTax), value);
        let total = 0;
        if(post.taxIncluded === 0) // for ++ price
            total = value+includeTax;
        else
            total = value;

        return total;
    }

    // const paymentOptions =  post.paymentOpt
    //     .map(item => {
    //     return (
    //         <div key={item.id} className="center">
    //             <img src={item.image} alt="" className="bank-logo" onError={(e)=>{e.target.onerror = null; e.target.src="https://api.ichronoz.net/images/no-image.png"}} />
    //         </div>
    //     );
    // });

    const optServices = booking.optServices.length > 0 && booking.optServices
        .map((srvc, index) => {
        return (
            <div key={index}>
                <div className="rate-items">
                    <span className="rate-badge">
                        <NumberFormat decimalScale={0} value={srvc.rate*srvc.qty} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                    </span>
                    <span ><b>{srvc.name}</b><br /></span>
                    <span>
                        {srvc.qty} {srvc.unit} @ 
                        <NumberFormat decimalScale={0} value={srvc.rate*1} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                    </span>
                </div>
            </div>
        )
    });

    const [ payUponCheckin ] = useState(() => {
        return (parseInt(post.paymentType) === 1 || parseInt(post.paymentType) === 3 || parseInt(post.paymentType) === 2) ? false : true;
    }); 

    return (  
        <div className="chr_rsv-summary">
            
            
            <div className="row white z-depth-2">
                <div className="col s12">
                    <div className="chr_discount-badge">
                        <span className={ classNames({ hiddenText: post.oldRate <= post.rate }) }>
                            {PercentRate(post.oldRate, post.rate)}% Discount
                        </span>
                    </div> 
                    <div className="row chr_mt10" style={{marginBottom: '15px'}}>
                        <div className="col s12">
                            <div className="rate-list">

                                <div className="rate-items">
                                    <span className="rate-badge old-price">
                                        <NumberFormat decimalScale={0} value={post.oldRate*post.rooms} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                    </span>
                                    Original price ({post.rooms} {post.accType} x {post.night} Night)
                                </div>

                                <div className={classNames({'rate-items': post.hid.utc_rate >= 100,hiddenText: post.hid.utc_rate < 100})}>
                                    <span className="rate-badge old-price">
                                        <NumberFormat decimalScale={0} value={post.hid.utc_rate+(post.rate*post.rooms)} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                    </span>
                                    Our price
                                </div>

                                <div className={classNames({'rate-items promo-code': post.hid.utc_rate >= 100,hiddenText: post.hid.utc_rate < 100})}>
                                    <span className="rate-badge">
                                        -<NumberFormat decimalScale={0} value={post.hid.utc_rate} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                    </span>
                                    <i className="material-icons tiny-l prefix">check_circle</i>&nbsp;&nbsp;
                                    <span className="chr_text-uppercase">{post.hid.utc_code}</span> promocode applied
                                </div>

                                <div className="separate-box">
                                    <div className="line-inner-box"></div>    
                                </div>

                                <div className="rate-items">
                                    <span className="rate-badge">
                                        <NumberFormat decimalScale={0} value={post.rate*post.rooms} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                    </span>
                                    <b>Price</b> ({post.rooms} {post.accType} x {post.night} Night)
                                </div>

                                {/* additional room upgrade deals */}
                                <div className={classNames({hiddenText: booking.breakfastQty > 0 ? false : true})}>
                                    <div className="rate-items">
                                        <span className="rate-badge">
                                            <NumberFormat decimalScale={0} value={booking.breakfastTotal} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                        <span ><b>Breakfast</b><br /></span>
                                        <span>
                                            {booking.breakfastQty} Pax @ 
                                            <NumberFormat decimalScale={0} value={booking.breakfastRate} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                    </div>
                                </div>

                                <div className={classNames({hiddenText: booking.extraBedQty > 0 ? false : true})}>
                                    <div className="rate-items">
                                        <span className="rate-badge">
                                            <NumberFormat decimalScale={0} value={booking.extraBedTotal} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                        <span ><b>Extrabed</b><br /></span>
                                        <span>
                                            {booking.extraBedQty} Pax @ 
                                            <NumberFormat decimalScale={0} value={booking.extraBedRate} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                    </div>
                                </div>

                                {/* Optional */}
                                <div>
                                    {optServices}
                                </div>

                                {/* Taxes */}
                                <div className={classNames({'rate-items': parseInt(post.taxIncluded) === 0, hiddenText: parseInt(post.taxIncluded) === 1})}>
                                    <span className="rate-badge">
                                        <NumberFormat decimalScale={0} value={onePajak(parseInt(post.srvcTax), parseInt(post.govTax), getTotalBeforeTax(), 'srvc_charge')} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                    </span>
                                    Service Charge
                                </div>
                                
                                <div className={classNames({'rate-items': parseInt(post.taxIncluded) === 0, hiddenText: parseInt(post.taxIncluded) === 1})}>
                                    <span className="rate-badge">
                                        <NumberFormat decimalScale={0} value={onePajak(parseInt(post.srvcTax), parseInt(post.govTax), getTotalBeforeTax(), 'gov_tax')} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                    </span>
                                    Goverment Tax
                                </div>

                                <div className={classNames({'rate-items': parseInt(post.taxIncluded) === 1, hiddenText: parseInt(post.taxIncluded) === 0 })}>
                                    <span className="rate-badge green-text">
                                        FREE
                                    </span>
                                    Booking fees
                                </div>
                                
                                <div className="separate-box">
                                    <div className="line-inner-box"></div>    
                                </div>

                                {/* total payabel */}
                                <div className={classNames({hiddenText: post.payAtHotel === true})} >
                                    <div className="rate-items total-pay">
                                        <span className="rate-badge">
                                            <NumberFormat decimalScale={0} value={getTotal()} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                        Total Payable
                                        <small className={classNames({hiddenText: parseInt(post.taxIncluded) === 0})}>
                                            <br/>
                                            <b>Included in price:</b> Service charge {post.srvcTax}%, Tax {post.govTax}%
                                        </small>
                                    </div>
                                </div>

                                {/* Payment type paid later OR Guarantee booking deposit */}
                                <div className={classNames({hiddenText: post.payAtHotel === true || parseInt(post.paymentType) === 0 || parseInt(post.paymentType) === 2})} >
                                    <div className="rate-items total-pay">
                                        <span className="rate-badge">
                                            <NumberFormat decimalScale={0} value={depositGuarantee(post.guaranteeOpt, taxInclude( (post.rate/post.night)*post.rooms ), post.guarantee , getTotal(), post)} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                        {post.guaranteeText}
                                    </div>
                                </div>

                                <div className={classNames({hiddenText: payUponCheckin})} >
                                    <div className="rate-items total-pay">
                                        <span className="rate-badge">
                                            <NumberFormat decimalScale={0} value={getTotal() - depositGuarantee(post.guaranteeOpt, taxInclude( (post.rate/post.night)*post.rooms ), post.guarantee, getTotal(), post)} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                        Pay upon check-in
                                    </div>
                                </div>
                                
                            </div>

                        </div>
                    </div>
                    
                </div>
            </div>

            {/* payment options */}
            {/* <div className="row white z-depth-2">
                <div className="col s12">
                    <div className="chr_border_top_white">
                        <div className="chr_text-center">
                            {paymentOptions}
                            
                        </div>
                    </div>
                </div>
            </div> */}

        </div>
        
    );
}

ReservationRateSummary.propTypes = {
    post: PropTypes.object
};

export default ReservationRateSummary;