import React from 'react';
import ModalContentMobile from './ModalContentMobile';
import ModalTermsMobile from './ModalTermsMobile';
import RatePartMobileMini from './RatePartMobileMini';
import PropTypes from 'prop-types';

const ImagePartMobile = ({post, openPromoDetail, promoDetailIsOpen, showAll, promoTermsIsOpen, openPromoTerms}) => {

    let classNames = require('classnames');

    return (  
        <div>
            <div className="chr_image-list">
                
                <div className="chr_image-card grow" onClick={openPromoDetail}>
                    <a className="modal-trigger" href='#!'>
                        <img width="100%" style={{maxHeight:'200px'}} src={post.img} alt="" className="chr_img-responsive responsive-img" />
                    </a>
                    
                </div>
                
                <div className="chr_special-tag">
                    <span className={ classNames({ hiddenText: post.specialName !== '' }) }> {post.promoType} </span>
                    <span className={ classNames({ hiddenText: post.specialName === '' }) }> {post.specialName} </span>
                </div>

                <div className={ classNames({hiddenText: showAll, 'rate-tag': true}) }>
                    <RatePartMobileMini post={post}/>
                </div>
            </div>

            <ModalContentMobile promoDetailIsOpen={promoDetailIsOpen} post={post} openPromoDetail={openPromoDetail} />            
            
            <ModalTermsMobile promoTermsIsOpen={promoTermsIsOpen} post={post} openPromoTerms={openPromoTerms} />

        </div>
        
        
    );
}

ImagePartMobile.propTypes = {
    post: PropTypes.object,
    openPromoDetail: PropTypes.func,
    promoDetailIsOpen: PropTypes.bool,
    openPromoTerms: PropTypes.func,
    promoTermsIsOpen: PropTypes.bool,
    showAll: PropTypes.bool,
};
 
export default ImagePartMobile;