import React, { createContext, useState, useEffect} from 'react';
import axios from 'axios';
import PropTypes from 'prop-types';
import moment from 'moment';

export const PromotionContext = createContext();

const PromotionContextProvider = (props) => {

    let tomorrow  = moment(new Date()).add(1,'days');

    let afterTomorrow  = moment(new Date()).add(2,'days');

    const [ exchangeRate, setExchangeRate ] = useState(1);

    const [ exchangeCurrency, setExchangeCurrency ] = useState('EUR');

    const [ apikey, setApikey ] = useState('e972a19b09ea90cf3ff34ec12359bd7b1');

    const [ promotions, setPromotions ] = useState([]);

    const [ infos, setInfos ] = useState([]);

    const [ apistatus, setApistatus ] = useState('error');

    const [ searchParams, setSearchParams ] = useState(() => {
        const localData = localStorage.getItem('last-search-params');
        let localDataJson = JSON.parse(localData);
        let api = localDataJson && typeof localDataJson.api !== 'undefined' ? localDataJson.api : '';
        let apikey = 'e972a19b09ea90cf3ff34ec12359bd7b1';

        return localData && apikey === api ? JSON.parse(localData) : {
            startDate: tomorrow.format('ddd, MMM DD YYYY'), 
            endDate: afterTomorrow.format('ddd, MMM DD YYYY'), 
            adu: 1, 
            utc: '',
            chi:0,
            fd:tomorrow.format("D"),
            fm:tomorrow.format("M"),
            fy:tomorrow.format("YYYY"),
            td:afterTomorrow.format("D"),
            tm:afterTomorrow.format("M"),
            ty:afterTomorrow.format("YYYY"),
            id:'',
            pax:1,
            promo:'',
            suretoremove:true,
            api: apikey
        }
    });

    const [ isPromotionLoading, setIsPromotionLoading ] = useState(false);

    const changePromocode = (value) => {
        setSearchParams({...searchParams, utc: value});

        if(value === '')
            searchPromotion();
        
    }

    const searchPromotion = () => {        
        setIsPromotionLoading(true);
        // changeSearch(searchs);
        // axios.get('https://jsonplaceholder.typicode.com/posts')
        // console.log('utc nateh====>', searchParams);
        axios.get('https://api.ichronoz.net/availabilityV3/v3?&adu='+searchParams.adu+'&api='+searchParams.api+'&chi='+searchParams.chi+'&fd='+searchParams.fd+'&fm='+searchParams.fm+'&fy='+searchParams.fy+'&id='+searchParams.id+'&pax='+searchParams.pax+'&promo='+searchParams.promo+'&td='+searchParams.td+'&tm='+searchParams.tm+'&ty='+searchParams.ty+'&url=&utc='+searchParams.utc)
            .then(res => {
                if(res.data.message === 'gagal'){
                    setInfos(res.data);
                    setIsPromotionLoading(false);
                }else{
                    
                    // console.log(res.data);
                    // console.log('ini promotions list:',promotions);

                    setPromotions(res.data.data);
                    setInfos(res.data.info);
                    setIsPromotionLoading(false);

                    searchExchangeRate();
                    setExchangeCurrency(()=>(res.data.info.cur === 'IDR') ? 'USD' : res.data.info.cur);
                }
                setApistatus(res.data.message);
                // document.getElementById('promotion-list').focus();
                // document.getElementsByClassName("tooltipped").tooltip();
                const $ = window.$;
                $('.scrollspy').scrollSpy();
                $('.tooltipped').tooltip();
                $('.materialboxed').materialbox();
                $('.modal').modal();
                $('.collapsible').collapsible();
                $('select').formSelect();
                // $('.carousel').carousel();
            })
            .catch(error => {
                console.log('ini datanya=>',error);
                alert('Sorry! we are under maintenance!!!', error);
                // setInfos(error);
                setApistatus('error');
                
                setIsPromotionLoading(false);
            });
        
    }

    const searchExchangeRate = (fromnya='USD', tonya='IDR') => {        
        
        if(fromnya === "IDR"){
            let tmp = fromnya;
            fromnya = tonya;
            tonya = tmp;
        }
        axios.get('https://api.ichronoz.net/exchangeV2?curFrom='+fromnya+'&curTo='+tonya+'&api=e972a19b09ea90cf3ff34ec12359bd7b1')
            .then(res => {
                
                // console.log('exchange:',res.data.rate);
                setExchangeRate(res.data.rate);

            })
            .catch(error => {
                console.log('Sorry! we are under maintenance [exchange rate]', error);
                
            });
    }

    const updatePromotions = (data) => {
        setPromotions(data);
    }

    useEffect(() => {        
        localStorage.setItem('last-search-params', JSON.stringify(searchParams));
        // searchPromotion();
    }, [searchParams]);

    useEffect(() => {        
        console.log('Hit API');
        // console.log('Hit API', apistatus);
    }, [apistatus]);

    return (  
        <PromotionContext.Provider value={{ promotions, searchPromotion, isPromotionLoading, searchParams, infos, apikey, setApikey, exchangeRate, searchExchangeRate, exchangeCurrency, setExchangeCurrency, apistatus, changePromocode, setSearchParams, updatePromotions }}>
            {props.children}
        </PromotionContext.Provider>
    );
}

PromotionContextProvider.propTypes = {
    children: PropTypes.node,
};

export default PromotionContextProvider;