import React, { useState, useContext, useEffect, useCallback } from 'react';
import { PromotionContext } from '../../contexts/PromotionContext';
import NumberFormat from 'react-number-format';
import { CurrencyOptions } from '../Helper';
import PropTypes from 'prop-types';
import Select from 'react-select';

const CurrencyConverter = ({post}) => {

    const customStyles = {
        control: base => ({
            ...base,
            minHeight: 30
        }),
        dropdownIndicator: base => ({
            ...base,
            padding: 4
        }),
        clearIndicator: base => ({
            ...base,
            padding: 4
        }),
        valueContainer: base => ({
            ...base,
            padding: '0px 16px'
        }),
        input: base => ({
            ...base,
            margin: 0,
            padding: 0
        }),
        menu: base => ({
            ...base,
            maxHeight: '120px'
        }),
        menuList: base => ({
            ...base,
            maxHeight: '120px',
        }),
    };

    const [ country ] = useState(CurrencyOptions);

    const { exchangeRate, searchExchangeRate } = useContext(PromotionContext);

    const [ fromCurrency, setFromCurrency ] = useState(post.cur);

    const [ toCurrency, setToCurrency ] = useState('USD');

    const [ fromValue ] = useState(post.rate*post.rooms);

    const [ toValue, setToValue ] = useState(1);

    const changeFromCurrency = (from) => {
        setFromCurrency(from);
        searchExchangeRate(from, toCurrency);
    }

    const changeToCurrency = (to) => {
        setToCurrency(to);
        searchExchangeRate(fromCurrency, to);
    }

    const nilaiExchange = useCallback(
        (rate = fromValue) => {
        let nilai = 0;
        if(post.cur === 'IDR')
            nilai = rate/exchangeRate;
        else
            nilai = rate*exchangeRate;
        // nilai = rate*exchangeRate;

        return nilai;
    }, [exchangeRate, fromValue, post.cur])

    const getSymbol = (currency) => {
        let cur = country.find(el => {  
            let nilai = '';
            if(el.value === currency)  {
                nilai = el;
            }
            return nilai
        });
        return (cur !== '-') ? cur.symbol : '';
    }

    useEffect(() => {
        setToValue(nilaiExchange());
        // console.log('currency converter');
    },[exchangeRate, nilaiExchange])

    return (
        <div>

            <div id={'modal_currency'+post.id} className="chr_modal modal bottom-sheet">
            
                <div className="modal-header modal-close">
                    <a href="#!" className="close-buton btn waves-effect waves-light black">Close</a>
                </div>
                <div className="modal-content">
                    <div className="row">
                        <div className="col s12">
                            <div className="desc-content">
                                <div className="desc-content-header" style={{padding:'20px 30px'}}>
                                    <div className="row">
                                            <div className="col 12 s12">
                                                <p className='header-title'> <b>Currency Converter</b> &nbsp;&nbsp;&nbsp; <span className='header-title-note'>{new Date().toLocaleString()}</span></p>
                                            </div>
                                            <div className="col m5 s12">
                                                <div className="label">
                                                    <p>From:</p>
                                                </div>
                                                <div>
                                                    <Select 
                                                        placeholder='Select Currency'
                                                        defaultValue={country.filter(c => c.value === fromCurrency)}
                                                        options={country} 
                                                        onChange={(e)=>{return changeFromCurrency(e.value)}}
                                                        styles={customStyles}
                                                    />
                                                </div>
                                                <div className="text-exchange">
                                                    <h4>
                                                        <span className="cur-symbol">{getSymbol(fromCurrency)}</span>
                                                        <NumberFormat value={fromValue.toFixed(2)} displayType={'text'} thousandSeparator={true} />
                                                    </h4>
                                                </div>
                                            </div>

                                            <div className="col m2 s12">
                                                <div className="valign-wrapper center-align" style={{display: 'inherit', paddingTop:'30px'}}>
                                                    <i className="medium material-icons">compare_arrows</i>
                                                </div>
                                                    
                                            </div>
                                            
                                            <div className="col m5 s12">
                                                <div className="label">
                                                    <p>To:</p>
                                                </div>
                                                <div>
                                                    <Select 
                                                        placeholder='Select Currency'
                                                        defaultValue={country.filter(c => c.value === toCurrency)}
                                                        options={country} 
                                                        onChange={(e)=>{return changeToCurrency(e.value)}}
                                                        styles={customStyles}
                                                    />
                                                </div>
                                                <div className="text-exchange">
                                                    <h4>
                                                        <span className="cur-symbol">{getSymbol(toCurrency)}</span>
                                                        <NumberFormat value={toValue.toFixed(2)} displayType={'text'} thousandSeparator={true} />
                                                    </h4>
                                                </div>
                                            </div>                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="modal-footer">
                    Note : Approximate rate, bank may charge with different rate.
                </div>
            </div>
        </div>
    );
}

CurrencyConverter.propTypes = {
    post: PropTypes.object
};
    
export default CurrencyConverter;