import React from 'react';
import PropTypes from 'prop-types';
// import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import { isMobile } from "react-device-detect";

const Banner = ({infos}) => {

    const imageStyleMobile = {
        height: '149px',
        maxHeight:'219px'
    };
    const imageStyle = {
        maxHeight:'219px'
    };

    return ( 
        <Carousel className="chr_banner">
            {infos.promoBanner && infos.promoBanner.map((slide, key) => {
                return (
                    <div key={key}>
                        <img style={isMobile ? imageStyleMobile : imageStyle} src={slide.url} alt="" />
                    </div>
                )
            })}
        </Carousel>
    );
}

Banner.propTypes = {
    infos: PropTypes.object
};
 
export default Banner;