import React, { useState } from 'react';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';

const OptionalServicesItems = ({post, srvc, rateValue, handleOptServices}) => {

    let classNames = require('classnames');

    const [isDisplay, setIsDisplay] = useState(true);

    const handleDisplay = () => {
        setIsDisplay(!isDisplay);
        console.log(isDisplay);
    }

    return ( 
        <div className="row chr_mt10" style={{marginBottom: '15px'}}>
            <div className="col l3 hide-on-med-and-down">
                <img width="100%" style={{maxHeight:'200px'}} src={srvc.pic} alt="" className="chr_img-responsive responsive-img" onError={(e)=>{e.target.onerror = null; e.target.src="https://api.ichronoz.net/images/no-image.png"}} />
            </div>
            <div className="col l9 m12">
                <div className="row">
                    <div className="col l8 m12">
                        <div className="chr_optional-rate">{srvc.name} </div>
                        <div className="chr_optional-rate">
                            <span>@ 
                            <NumberFormat value={srvc.rate} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            &nbsp; for <b>{rateValue(srvc.idAdd, 'qty')}</b> {srvc.unit}</span>

                            &nbsp;&nbsp;&nbsp;&nbsp;
                            <span className="chr_btn-active">
                                <i className={ classNames({ 'blue-text text-darken-2 material-icons':true }) } onClick={()=> {handleOptServices(srvc.idAdd, 'remove')}}>
                                    remove_circle_outline
                                </i>
                            </span>
                            <span className={classNames({hiddenText: 0 >= rateValue(srvc.idAdd, 'qty') || rateValue(srvc.idAdd, 'qty') >= srvc.maxUnit  })}>
                                <span className={classNames({'chr_btn-active': true })} onClick={()=> {handleOptServices(srvc.idAdd)}}>
                                    <i className={classNames({'blue-text text-darken-2 material-icons': true})}>control_point</i>
                                </span>
                            </span>
                        </div>
                        <div className={ classNames({ hiddenText: isDisplay, 'chr_text-justify': true }) }>
                            <div dangerouslySetInnerHTML={{__html: srvc.desc}}></div>
                            <span className="chr_btn-active blue-text text-darken-2" onClick={handleDisplay}> Less... <i className="material-icons">keyboard_arrow_up</i></span>
                        </div>
                        <div className={ classNames({ hiddenText: !isDisplay, 'chr_text-justify': true }) }>
                            <div dangerouslySetInnerHTML={{__html: srvc.desc.substring(0, 130)}}></div>
                            <span className={ classNames({ hiddenText: srvc.desc.length < 130, 'chr_btn-active blue-text text-darken-2': true }) } onClick={handleDisplay}> Show more... <i className="material-icons">keyboard_arrow_down</i></span>
                        </div>
                        
                    </div>
                    <div className="col l4 m12">
                        <div >
                            <span className="optional-price">
                                + <NumberFormat value={rateValue(srvc.idAdd)} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            </span>
                        </div>
                        <br />
                        <div className="right">
                            <span onClick={()=> {handleOptServices(srvc.idAdd)}} className={classNames({'hiddenText': rateValue(srvc.idAdd, 'qty') > 0,'waves-effect waves-light btn btn-small animated tada blue lighten-3 valign-wrapper center-align chr_btn-active': true })}> 
                                &nbsp;&nbsp;Add&nbsp;&nbsp;
                            </span>
                            <span onClick={()=> {handleOptServices(srvc.idAdd, 'remove')}} className={classNames({'hiddenText':  0 >= rateValue(srvc.idAdd, 'qty'),'waves-effect waves-light btn btn-small animated tada blue lighten-3 valign-wrapper center-align chr_btn-active': true })}> 
                                Remove
                            </span>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    );
}

OptionalServicesItems.propTypes = {
    post: PropTypes.object,
    srvc: PropTypes.object,
    rateValue: PropTypes.func,
    handleOptServices: PropTypes.func
};
 
export default OptionalServicesItems;