import React from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import NumberFormat from 'react-number-format';
import { PercentRate } from '../Helper';
import PropTypes from 'prop-types';

const ModalContent = ({post, type=''}) => {

    return (  
        <div id={'modal'+post.id+type} className="chr_modal modal bottom-sheet">
        
            <div className="modal-header modal-close">
                <a href="#!" className="close-buton btn waves-effect waves-light black">Close</a>
            </div>
            <div className="modal-content">
                
                <div className="row">
                    <div className="col l7 s12">
                        <div className="image-content">
                            <Carousel>
                                {post.gallery.map((image, key) => {
                                    return (
                                        <div key={key}>
                                            <img style={{maxHeight:'350px'}} src={image} alt="" onError={(e)=>{e.target.onerror = null; e.target.src="https://api.ichronoz.net/images/no-image.png"}} />
                                        </div>
                                    )
                                })}
                            </Carousel>
                            <div className="chr_special-tag-modal">
                                SAVE {PercentRate(post.oldRate, post.rate)}% <br /> Today
                            </div>
                        </div>
                    </div>
                    <div className="col l5 s12">

                        <div className="desc-content">
                            <div className="desc-content-header">
                                <p className="final-price-text">{post.name}</p>
                                {/* <p>
                                    {post.rooms} 
                                    <span style={{ display: post.rooms < 2 ? '' : 'none' }}>{post.accType}s</span>
                                    <span> for {post.bookingDate} ({post.night} Nights)</span>
                                </p> */}
                                <p>
                                    <span className="orange-text darken-3"> @avg {post.cur}.</span>
                                    <span className="orange-text darken-3 final-price-text">{(post.rate*post.rooms)/post.night}</span>
                                    <span className="orange-text darken-3">/night</span>
                                </p>
                                
                            </div>	

                            <div className="panel-body-custom descriptionCustom scroll">
                                <div dangerouslySetInnerHTML={{__html: post.typeDesc}}></div>
                            </div>	
                        </div>

                    </div>
                </div>
                <div className="row" style={{borderTop: '1px solid #CCC'}}>
                    <div className="col s12">
                        <div className="desc-content chr_detail-rate-box">
                            {post.allotments.map((rateList, key) => {
                                return (
                                    <div className="chr_detail-rate grey darken-1" key={key}>
                                        {rateList.dates} <br /> <NumberFormat value={rateList.rate} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                    </div>
                                    
                                )
                            })}
                        </div>
                        
                    </div>
                </div>
            </div>
            
        </div>
    );
}

ModalContent.propTypes = {
    post: PropTypes.object,
    type: PropTypes.object
};
 
export default ModalContent;