import React from 'react';
import NumberFormat from 'react-number-format';
import { isMobile } from "react-device-detect";
import CurrencyConverter from '../CurrencyConverter';
import { Link } from "react-router-dom";
import PropTypes from 'prop-types';

const RatePart = ({post}) => {
    let classNames = require('classnames');

    return (  
        <div>
            <div className='text-right'>
                <div className={ classNames({ hiddenText: post.depositText === '', chr_mb9:true }) } >
                    <p className="chr_label green">
                        <i className="material-icons tiny-s perfix">local_offer</i>&nbsp;&nbsp;
                        {post.depositText}
                    </p>
                </div>   
                <div >
                    <a className="tooltipped " data-tooltip="Standard Publish Price" href="#!" >
                        <p className="old-price">
                            <NumberFormat decimalScale={0} value={post.oldRate*post.rooms} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            <span className={ classNames({ hiddenText: post.taxIncluded !== '0' }) }>++</span>
                        </p>	
                    </a>
                </div>
                <div className={ classNames({ hiddenText: post.hid.utc_rate < 1 }) }>
                    <a href="#!" className="tooltipped " data-tooltip="Price before promocode disc. is applied" >
                        <p className="old-price" >
                            <NumberFormat decimalScale={0} value={post.hid.utc_rate+(post.rate*post.rooms)} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            <span className={ classNames({ hiddenText: post.taxIncluded !== '0' }) }>++</span>
                        </p>	
                    </a>
                        
                </div>
                <div >
                    <a href="#!" className="tooltipped " data-tooltip={post.hid.finalPriceHoverText} >
                        {/* <i className="fa fa-question-circle" className={ classNames({ hiddenText: post.hid.utc_rate < 1 }) }></i> */}
                        <p className="final-price">
                            <NumberFormat decimalScale={0} value={post.rate*post.rooms} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                            <span className={ classNames({ hiddenText: post.taxIncluded !== '0' }) }>++</span>
                        </p>
                    </a>
                </div>
                <div>
                    <p>
                        <a className="room-details modal-trigger" href={'#modal_currency'+post.id}> Currency Converter  </a>
                    </p>
                </div>
                <div>
                    <Link className={ classNames({ 'waves-effect waves-light btn btn-large animated tada blue darken-3 valign-wrapper center-align btn-chrbook': true, 'btn-block': isMobile  }) } to={'/reservation/'+post.id}>
                        Book Now
                    </Link>
                </div>
            </div>

            <CurrencyConverter post={post} NumberFormat={NumberFormat}/>

        </div>
        
    );
}

RatePart.propTypes = {
    post: PropTypes.object
};

export default RatePart;