import React, { useState } from 'react';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';

const OptionalServicesItemsMobile = ({post, srvc, rateValue, handleOptServices}) => {

    let classNames = require('classnames');

    const [isDisplay, setIsDisplay] = useState(true);

    const handleDisplay = () => {
        setIsDisplay(!isDisplay);
        console.log(isDisplay);
    }

    return ( 
        <div className="chr_mb6">
            
                <div className={classNames({'chr_image-list': true})}>
                    
                    <div className="chr_image-card">
                        <span onClick={handleDisplay}>
                            <img width="100%" style={{maxHeight:'200px'}} src={srvc.pic} alt="" className="chr_img-responsive responsive-img" />
                        </span>
                        
                    </div>
                    

                    <div className={ classNames({'rate-tag-optional': true}) }>
                        <div className="minishow">
                            <div className="row chr_mt10" style={{marginBottom: '15px'}}>
                                <div className="col l7 m12">
                                    <div className="">
                                        <p onClick={handleDisplay}>
                                            {srvc.name}
                                        </p>
                                        
                                        <p>
                                            <span onClick={handleDisplay}>@ 
                                                <NumberFormat value={srvc.rate*1} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                                &nbsp;&nbsp;&nbsp;<b>{rateValue(srvc.idAdd, 'qty')}</b> {srvc.unit}
                                            </span>
                                            
                                        </p>
                                        
                                    </div>
                                </div>
                                <div className="col l5 m12" style={{float:'right'}}>
                                    <div >
                                        <span className="final-price-optional">
                                        + <NumberFormat value={rateValue(srvc.idAdd)*1} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                        </span>
                                    </div>
                                    <div className="right">
                                        <span className="chr_btn-active">
                                            <i className={ classNames({ 'blue-text text-darken-2 material-icons':true }) } onClick={()=> {handleOptServices(srvc.idAdd, 'remove')}}>
                                                remove_circle_outline
                                            </i>
                                        </span>
                                        <span className={classNames({hiddenText: rateValue(srvc.idAdd, 'qty') >= srvc.maxUnit  })}>
                                            <span className={classNames({'chr_btn-active': true })} onClick={()=> {handleOptServices(srvc.idAdd)}}>
                                                <i className={classNames({'blue-text text-darken-2 material-icons': true})}>control_point</i>
                                            </span>
                                        </span>    
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                </div>

                <div onClick={handleDisplay} className={ classNames({ hiddenText: isDisplay, 'chr_text-justify blue lighten-5 chr_p10 ': true }) }>
                    <div dangerouslySetInnerHTML={{__html: srvc.desc}}></div>
                    <span className="chr_btn-active blue-text text-darken-2" onClick={handleDisplay}> Less... <i className="material-icons">keyboard_arrow_up</i></span>
                </div>
                
            
        </div>
    );
}

OptionalServicesItemsMobile.propTypes = {
    post: PropTypes.object,
    srvc: PropTypes.object,
    rateValue: PropTypes.func,
    handleOptServices: PropTypes.func
};
 
export default OptionalServicesItemsMobile;