import React, { useState, useContext, useRef } from 'react';
import { PromotionContext } from '../../contexts/PromotionContext';
import { Redirect, Link } from "react-router-dom";
import PromoSummary from '../sub-page/PromoSummary';
import Navbar from '../sub-page/Navbar';
import { BrowserView,MobileView } from "react-device-detect";

import PropTypes from 'prop-types';
import PaymentOptions from '../sub-page/PaymentOptions';
import RoomUpgradeDeal from '../sub-page/RoomUpgradeDeal';
import ReservationRateSummary from '../sub-page/ReservationRateSummary';
import OptionalServices from '../sub-page/OptionalServices';
import NavbarMobile from '../mobile/NavbarMobile';
import GuestInfo from '../sub-page/GuestInfo';
import TimeoutPage from '../sub-page/TimeoutPage';

// const scrollToRef = (ref) => window.scrollTo(0, (ref.current.offsetTop) ? ref.current.offsetTop : 0) 

const Reservation = ({match}) => {
    
    const { promotions } = useContext(PromotionContext);

    const myFocus = useRef(null)

    const {
        params: { post_id },
    } = match;

    const [ promo ] = useState(promotions.find(item => item.id === post_id));

    // useEffect(() => {
    //     scrollToRef(myFocus);
    // }, [])

    return promo ? (  
        <div>
            
            <BrowserView>
                <Navbar />
            </BrowserView>      
            <MobileView>
                <NavbarMobile />
            </MobileView>
            <div className="container chronoz" style={{paddingTop:'20px'}} id="reservation">
               <div className="row">
                    <div id="stepper-example" className="bs-stepper">
                        <div className="bs-stepper-header">
                            <div className="step">
                                <div className="active">
                                    <Link to={'/promotions'}>
                                        <span className="bs-stepper-circle">
                                            <i className="material-icons tiny-s">insert_drive_file</i>
                                        </span>
                                        <span className="bs-stepper-label">Reservation</span>    
                                    </Link>
                                </div>
                            </div>
                            <div className="line"></div>
                            <div className="step">
                                
                                <span className="bs-stepper-circle">
                                    <i className="material-icons tiny-s">credit_card</i>
                                </span>
                                <span className="bs-stepper-label">Payment</span>
                                
                            </div>
                            <div className="line"></div>
                            <div className="step">
                                
                                <span className="bs-stepper-circle">
                                    <i className="material-icons tiny-s">playlist_add_check</i>
                                </span>
                                <span className="bs-stepper-label">Confirmation</span>
                                
                            </div>
                        </div>
                        
                    </div>
                </div>
                <div className="row" >
                
                <BrowserView>
                    <div className="col l8 s12">

                        <TimeoutPage />

                        <RoomUpgradeDeal post={promo} />

                        <OptionalServices post={promo} />

                        <div ref={myFocus}>
                            <GuestInfo post={promo} />
                        </div>

                        <PaymentOptions post={promo} />

                        {/* <ProceedButton /> */}

                    </div>
                    <div className="col l4 s12">
                        
                        <PromoSummary post={promo} />

                        <ReservationRateSummary post={promo} />
                        
                    </div>
                </BrowserView>
                <MobileView>
                    <div className="col s12">

                        <TimeoutPage />

                        <PromoSummary post={promo} />

                        <ReservationRateSummary post={promo} />

                        <RoomUpgradeDeal post={promo} />

                        <OptionalServices post={promo} />

                        <div ref={myFocus}>
                            <GuestInfo post={promo} />
                        </div>                        

                        <PaymentOptions post={promo} />

                        {/* <ProceedButton /> */}

                    </div>
                </MobileView>
                </div>

                {/* <Photo />
                <Parallax image="/img/stars.jpg"/> */}
            </div>
        </div> 
    ):(
        <div>
            <Redirect to='/promotions' />
        </div>
    );
}

Reservation.propTypes = {
    match: PropTypes.object,
};

export default Reservation;