import React from 'react';
import ModalContent from '../ModalContent';
import PropTypes from 'prop-types';
import ModalTerms from '../ModalTerms';

const ImagePart = ({post}) => {

    let classNames = require('classnames');

    return (  
        <div>
            <div className="chr_image-list">
                
                <div className="chr_image-card grow">
                    <a className="modal-trigger" href={'#modal'+post.id}>
                        <img width="100%" style={{maxHeight:'200px'}} src={post.img} alt="" className="chr_img-responsive responsive-img" onError={(e)=>{e.target.onerror = null; e.target.src="https://api.ichronoz.net/images/no-image.png"}} />
                    </a>
                    
                </div>
                
                <div className="chr_special-tag">
                    <span className={ classNames({ hidden: post.specialName !== '' }) }> {post.promoType} </span>
                    <span className={ classNames({ hidden: post.specialName === '' }) }> {post.specialName} </span>
                </div>
            </div>

            <div>
                <p>
                    <i className="material-icons tiny-l">settings_overscan</i>&nbsp;&nbsp; Room size: 18 m²/194 ft²
                </p>
                
            </div>
            
            <ModalContent post={post}/>

            <ModalTerms post={post} />

        </div>
        
        
    );
}

ImagePart.propTypes = {
    post: PropTypes.object
};
 
export default ImagePart;