import React, { useState } from 'react';
import {CSSTransition} from 'react-transition-group';
import PropTypes from 'prop-types';

const PromoDesc = ({post}) => {

    let classNames = require('classnames');

    const [isDisplay, setIsDisplay] = useState(true);

    const handleDisplay = () => {
        setIsDisplay(!isDisplay);
        console.log(isDisplay);
    }

    return (  
        <div className="chr_mt10">
            <CSSTransition
                in={isDisplay}
                timeout={350}
                classNames="trans"
                unmountOnExit
                >
                <div className={ classNames({ hiddenText: !isDisplay }) }>
                    <div dangerouslySetInnerHTML={{__html: post.desc.substring(0, 230)}}></div>
                    <p className={ classNames({ hiddenText: post.desc.length < 230 }) }>
                        <a href="#!" onClick={handleDisplay}> Show more... <i className="material-icons">keyboard_arrow_down</i></a>
                        
                    </p>
                </div>
            </CSSTransition>
            
            <CSSTransition
            in={!isDisplay}
            timeout={350}
            classNames="trans"
            unmountOnExit
            >
                <div className={ classNames({ hiddenText: isDisplay }) }>
                    <div dangerouslySetInnerHTML={{__html: post.desc}}></div>
                    <p>
                        <a href="#!" onClick={handleDisplay}> Less... <i className="material-icons">keyboard_arrow_up</i></a>
                        
                    </p>
                </div>
            </CSSTransition>
            
        </div>
    );
}

PromoDesc.propTypes = {
    post: PropTypes.object
};
 
export default PromoDesc;