import React from 'react';
import NumberFormat from 'react-number-format';
import { Carousel } from 'react-responsive-carousel';
import { PercentRate } from '../../Helper';
import PropTypes from 'prop-types';

const ModalContentMobile = ({post, promoDetailIsOpen, openPromoDetail}) => {
    
    let classNames = require('classnames');
    
    return (  
        <div className={ classNames({ hiddenText: !promoDetailIsOpen }) } id={'modal-mobile'+post.id}>
            <div className="modal-dialogs">
                <a href="#!" onClick={openPromoDetail} className="left-align waves-effect waves-light btn btn-large btn-block btn-back blue darken-2">
                    <i className="material-icons white-text left">chevron_left</i> 
                    Promotion Detail
                </a>

                <div className="optional-modal white modal-scroll" height="100%">
                    <div className="row">
                        <div className="col l7 s12">
                            <div className="image-content">
                                <Carousel showThumbs={false} autoPlay interval={2000} infiniteLoop>
                                    {post.gallery.map((image, key) => {
                                        return (
                                            <div key={post.id+'_'+key}>
                                                <img style={{maxHeight:'350px'}} src={image} alt="" />
                                            </div>
                                        )
                                    })}
                                </Carousel>
                                <div className="chr_special-tag-modal">
                                    SAVE {PercentRate(post.oldRate, post.rate)}% <br /> Today
                                </div>
                            </div>
                        </div>
                        <div className="col l5 s12">

                            <div className="desc-content">
                                <div className="desc-content-header">
                                    <p className="final-price-text">{post.name}</p>
                                    <p>
                                        <span className="orange-text darken-3"> @avg {post.cur}.</span>
                                        <span className="orange-text darken-3 final-price-text">{(post.rate*post.rooms)/post.night}</span>
                                        <span className="orange-text darken-3">/night</span>
                                    </p>
                                    
                                </div>	

                                <div className="panel-body-custom descriptionCustom">
                                    <div dangerouslySetInnerHTML={{__html: post.typeDesc}}></div>
                                </div>	
                            </div>

                        </div>
                    </div>
                    <div className="row">
                        <div className="col s12">
                            <div className="mobile-detail-rate">
                                <div className="desc-content chr_detail-rate-box">
                                    {post.allotments.map((rateList, key) => {
                                        return (
                                            <div className="chr_detail-rate grey darken-1" key={key}>
                                                {rateList.dates} <br /> <NumberFormat value={rateList.rate} displayType={'text'} thousandSeparator={true} prefix={post.cur+ " "} />
                                            </div>
                                            
                                        )
                                    })}
                                </div>
                                
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
        </div> 
    );
}

ModalContentMobile.propTypes = {
    post: PropTypes.object,
    promoDetailIsOpen: PropTypes.bool,
    openPromoDetail: PropTypes.func
};
 
export default ModalContentMobile;