import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { BookingContext } from '../../contexts/BookingContext';
import ProceedButton from './ProceedButton';

const PaymentOptions = ({post}) => {

    let classNames = require('classnames');

    let paymentOptionsLength = post.paymentOpt.length;

    const { booking, changePaymentOption } = useContext(BookingContext);

    const selectPaymentOption = (value) => {
        changePaymentOption(value);
    }

    return (  
        <div>
            <div className="row white z-depth-2">
                <div className="col s12">
                    <div className="header-box">
                        Choose your payment option
                    </div>
                    
                    {
                        
                        post.paymentOpt
                            .map((payment, key) => {
                            return (
                                <div post={payment} key={payment.id} className="row chr_mt10" style={{marginBottom: '15px'}}>
                                    <div className="col s6 chr_mb6">
                                        <p>
                                            <label>
                                                <input className="with-gap" name="selected_payment" type="radio" value={payment.id} onChange={(e)=>{selectPaymentOption(e.target.value)}} checked={booking.paymentOption === payment.id}/>
                                                <span>{payment.title}</span>
                                            </label>
                                        </p>
                                        
                                    </div>
                                    <div className="col s6 chr_mb6">
                                        <img style={{width:'130px'}} src={payment.image} alt={payment.title} className="chr_img-responsive responsive-img" onError={(e)=>{e.target.onerror = null; e.target.src="https://api.ichronoz.net/images/no-image.png"}} />
                                    </div>
                                    <div className={classNames({hiddenText: payment.id !== booking.paymentOption, 'col s12': true})} >
                                        <div dangerouslySetInnerHTML={{__html: payment.description}} className="blue lighten-5 chr_p10"></div>
                                    </div>
                                    <div className={classNames({hiddenText: paymentOptionsLength === key+1})}>
                                        <div className="separate-box">
                                            <div className="line-inner-box"></div>    
                                        </div>
                                    </div>
                                    
                                </div>
                            )
                        })
                    }
                    
                </div>

                <ProceedButton post={post} />
            </div>
        </div>
    );
}

PaymentOptions.propTypes = {
    post: PropTypes.object
};
 
export default PaymentOptions;