import React, { useContext, useState } from 'react';
import { PromotionContext } from '../../contexts/PromotionContext';
import PropTypes from 'prop-types';

const SummaryPromocode = ({infos}) => {

    let classNames = require('classnames');

    const { searchParams, setSearchParams, searchPromotion } = useContext(PromotionContext);

    const [tempUtc] = useState(searchParams.utc);
    
    const removePromocode = () => {        
        setSearchParams({...searchParams, utc: '', 'suretoremove':false});
    }

    const addPromocode = () => {        
        setSearchParams({...searchParams, utc: tempUtc, 'suretoremove':true});
    }

    const sureRemovePromocode = () => {
        searchPromotion();
        setSearchParams({...searchParams, utc: '', 'suretoremove':true});
    }

    return (  
        <div className={ classNames({ hiddenText: infos && infos.utc === '', 'chr_no_result_availability light-green lighten-3': true }) }>
            
            <div className="row" style={{marginBottom: '0px'}}>
                <div className="col l7 m12">
                    <div className="chr_ptb15" dangerouslySetInnerHTML={{__html: ( infos ) ? infos.promocode_message : ''}}></div>
                </div>
                <div className={ classNames({ 'yellow lighten-2': searchParams.suretoremove !== true, 'col l5 m12 right': true}) }>
                    <div className="chr_ptb15 promocode_remove">
                        <a href="#!" className={ classNames({ hiddenText: searchParams.suretoremove === false, 'waves-effect waves-teal btn-small green darken-1': true}) } onClick={removePromocode}>remove</a>
                        <span className={ classNames({ hiddenText: searchParams.suretoremove === true}) }>
                            Sure want to remove this promocode? 
                            &nbsp;&nbsp;
                            <a href="#!" className="waves-effect waves-light btn-small red darken-1" onClick={sureRemovePromocode}> YES</a>
                            &nbsp;&nbsp;
                            <a href="#!" className="waves-effect waves-light btn-small green darken-1" onClick={addPromocode}> NO</a>
                        </span>
                        
                    </div>
                </div>
            </div>
        </div>
    );
}

SummaryPromocode.propTypes = {
    infos: PropTypes.object
};
 
export default SummaryPromocode;